import * as React from 'react';
import * as moment from "moment";
import { ITMRepository } from '../api/TMRepository';
import { ITmServiceLinks } from '../api/IApi';
import { setStateAsync } from '../lib/Helpers';

interface ITreasuryManagementLinksProps
{
    shouldLoad: boolean;
    tmRepository: ITMRepository;
}

interface ITreasuryManagementLinksState
{
    tmLinks: ITmServiceLinks[] | null;
}

class TreasuryManagementLinks extends React.Component<ITreasuryManagementLinksProps, ITreasuryManagementLinksState> {
    private readonly _tmRepository: ITMRepository;

    constructor(props: ITreasuryManagementLinksProps)
    {
        super(props);
        this._tmRepository = props.tmRepository;
        this.state = {
            tmLinks: null
        }
    }

    componentWillReceiveProps(nextProps: ITreasuryManagementLinksProps)
    {
        if (nextProps.shouldLoad)
        {
            this.loadData();
        }
    }

    componentDidMount()
    {
        if (this.props.shouldLoad)
        {
            this.loadData();
        }
    }

    loadData = async () =>
    {
        const tmLinks = await this._tmRepository.getTmServiceArchiveLinks();
        await setStateAsync<ITreasuryManagementLinksState>(this, { tmLinks });
    }

    render()
    {
        if (this.state.tmLinks === null)
            return null;

        return (<div className="box">
            <div className="header">
                Archived Product List
            </div>
            <div className="body">
                <table className="table mb20">
                    <thead>
                        <tr className="headerRow">
                            <th>Treasury Management Name</th>
                            <th>Description</th>
                            <th>Created By</th>
                            <th>Created Date</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.tmLinks.map((x: ITmServiceLinks, i: number) =>
                        {
                            return <tr key={i} className="dataRow">
                                <td><a href={`/${x.tmService.value}`}>{x.tmService.label}</a></td>
                                <td>{x.description}</td>
                                <td><a href={`/${x.createdBy.value}`} target="_blank">{x.createdBy.label}</a></td>
                                <td>{moment(x.createdDate).format("MM-DD-YYYY hh:mm A")}</td>
                            </tr>
                        })}
                    </tbody>
                </table>
            </div>
        </div>);
    }
}


export default TreasuryManagementLinks;


