import * as React from "react";
import * as fa from "./common/FontAwesome";
import Modal from "./common/Modal";
import Button from "./common/Button";
import Dropdown from './common/Dropdown';
import Errors from "./common/Errors";
import { ValidationEnum } from "./common/InputField";
import OutForSignature, { ContactsData, PacketRequests, ISignerInfo } from "./OutForSignature";
import { setStateAsync } from "../lib/Helpers";
import { ITMRepository } from "../api/TMRepository";
import Textarea from "./common/Textarea";
import logger, { LogState } from "../lib/Logger";
import Loading from "./common/Loading";
import { removeTmProductFromPacket } from "../api/prod/api";

interface IStageModal {
    onSave: any;
    onCancel: any;
    onWithdraw: any;
    hasErrors: boolean;
    errors: string[];
    isOpen: boolean;
    stages: any[];
    currentStage: string;
    contactsData: ContactsData[];
    packetRequests: PacketRequests[];
    packet: any
    stageStepInfo: string;
    lastPrintDate?: number;
    tmRepository: ITMRepository;
}

interface IStageModalState {
    saving?: boolean;
    errors: any[];
    nextStage: string;
    nextStageName: string;
    notes: string;
    signerInfo: ISignerInfo | undefined;
    loading: boolean;
    removeMessage: string;
}

class StageModal extends React.Component<IStageModal, IStageModalState> {
    constructor() {
        super();
        this.state = {
            saving: false,
            errors: [],
            nextStage: "",
            nextStageName: "",
            signerInfo: undefined,
            notes: "",
            loading: true,
            removeMessage: ''
        }
        this.onSaving = this.onSaving.bind(this);
        this.onChangeStage = this.onChangeStage.bind(this);
        this.onSignerChange = this.onSignerChange.bind(this);
        this.onNotesChange = this.onNotesChange.bind(this);
    }


    async onSaving() {
        await setStateAsync(this, { errors: [], saving: true, removeMessage:'' });

        const { nextStageName } = this.state;
        if (nextStageName.toLowerCase() == "withdrawn") {
            let status = await this.props.tmRepository.removeTmProductFromPacket();
            this.props.onWithdraw();
        } else {
            const verifyErrors: string[] = this.verifyOutForSignature();

            if (verifyErrors.length == 0) {
                let { nextStage } = this.state;
                if (nextStage.length === 0) {
                    nextStage = this.props.stages[0].Id;
                }
                this.props.onSave(nextStage, this.state.signerInfo, this.state.notes);
            } else {
                await setStateAsync(this, { errors: verifyErrors, saving: false });
            }
        }

       

    }

    verifyOutForSignature = (): string[] => {
        const errors: string[] = [];

        const { nextStageName, signerInfo } = this.state;
        const { contactsData } = this.props;
        if (nextStageName.toLowerCase() == "out for signature") {
            if (signerInfo === undefined || signerInfo.tmRequests.length === 0) {
                errors.push("Please choose at least one product to send out for signature.");
                return errors;
            }

            if (signerInfo !== undefined && signerInfo.signer !== undefined && signerInfo.signer.length === 0) {
                errors.push("Please choose at least one signer.");
                return errors;
            }

            const firstSigner = signerInfo.signer.find(x => x.priority === 1);

            if (firstSigner === undefined)
                errors.push("Please choose a primary signer.");

            if (firstSigner !== undefined) {
                if (firstSigner.securityCode != undefined && firstSigner.securityCode.length < 4)
                    errors.push("Primary Signer Security Code must be between 4 and 6 digits.");

                const contact = contactsData.find(x => x["Contact.Id"] == firstSigner.id);

                //if (contact === undefined || contact["Contact.Email"] === undefined || contact["Contact.Email"].match("[a-zA-Z0-9\.\-]{2,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                if (contact === undefined || contact["Contact.Email"] === undefined || contact["Contact.Email"].match("[a-zA-Z0-9\.\-]{1,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                errors.push("Please choose a primary signer with a valid email address.");

            }

            const secondSigner = signerInfo.signer.find(x => x.priority === 2);

            if (secondSigner !== undefined) {
                if (secondSigner.securityCode != undefined && secondSigner.securityCode.length < 4)
                    errors.push("Secondary Signer Security Code must be between 4 and 6 digits.");

                const contact = contactsData.find(x => x["Contact.Id"] == secondSigner.id);

                //if (contact === undefined || contact["Contact.Email"] === undefined || contact["Contact.Email"].match("[a-zA-Z0-9\.\-]{2,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                if (contact === undefined || contact["Contact.Email"] === undefined || contact["Contact.Email"].match("[a-zA-Z0-9\.\-]{1,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                    errors.push("Please choose a secondary signer with a valid email address.");

            }

            const tmoSigner = signerInfo.signer.find(x => x.priority === 3);

            if (tmoSigner !== undefined) {
                if (tmoSigner.securityCode != undefined && tmoSigner.securityCode.length < 4)
                    errors.push("Bank Signer Security Code must be between 4 and 6 digits.");

                //if (tmoSigner.tmo === undefined || tmoSigner.tmo.Email === undefined || tmoSigner.tmo.Email.match("[a-zA-Z0-9\.\-]{2,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                if (tmoSigner.tmo === undefined || tmoSigner.tmo.Email === undefined || tmoSigner.tmo.Email.match("[a-zA-Z0-9\.\-]{1,}@[a-zA-Z0-9\.\-]{2,}\.[a-zA-Z]{2,}") === null)
                    errors.push("Please choose a bank signer with a valid email address.");
            }

            return errors;
        }

        return [];
    }

    async componentWillReceiveProps(nextProps: IStageModal) {
        if (nextProps.hasErrors) {
            if (nextProps.errors !== undefined && nextProps.errors[0].includes('Operation timed out. Please do')){
                await setStateAsync(this, { errors: ["", ...nextProps.errors], saving: false });
            } else {
                await setStateAsync(this, { errors: ["Please fix errors before continuing to next stage.", ...nextProps.errors], saving: false });
            }
            
        } else {
            await setStateAsync(this, { errors: [] });
        }

        if (!nextProps.isOpen) {
            await setStateAsync(this, { saving: false, nextStage: "", nextStageName: "", notes: "", loading: true });
        }

        if (nextProps.isOpen && nextProps.stages.length > 0 && this.state.nextStage.length === 0) {
            let stageNotes = await this.props.tmRepository.getStageNotes();
            logger.log(LogState.Debug, stageNotes);

            if (stageNotes["Notes"] === undefined)
                stageNotes.Notes = "";

            await setStateAsync(this, { loading: false, nextStage: nextProps.stages[0].Id, nextStageName: nextProps.stages[0].Name, notes: stageNotes.Notes });
        }
    }

    renderStageOptions() {
        var options: any[] = [];
        this.props.stages.forEach((stage: any) => {
            options.push({
                Value: stage.Id,
                Label: stage.Name
            });
        });

        return options;
    }

    async onChangeStage(e) {
        //let removeMessage = 'Withdraw will remove this product \'' + this.props.packet.Type + '\' (' + this.props.packet.Request.Name + ') '+' from packet. Continue ?';
        const selectedOption = e.target.options[e.target.selectedIndex];
        await setStateAsync(this, {
            nextStage: selectedOption.value, nextStageName: selectedOption.text,
            removeMessage: selectedOption.text.toLowerCase() == "withdrawn"?this.removeMessage():''
        });
    }

    async onSignerChange(signerInfo: ISignerInfo) {
        await setStateAsync(this, { signerInfo });
    }

    async onNotesChange(note: React.ChangeEvent<HTMLTextAreaElement>) {
        let { notes } = this.state;
        notes = note.currentTarget.value;

        await setStateAsync<IStageModalState>(this, { notes });
    }

    removeMessage = ():string => {
        return 'Withdraw will remove this product \'' + this.props.packet.Type + '\' (' + this.props.packet.Request.Name + ') ' + ' from packet. Continue ?';
    }

    async componentDidMount() {
        let currentStage = this.props.stages[0];
        if (currentStage.Name.toLowerCase() == 'withdrawn') {
            await setStateAsync(this, { removeMessage: this.removeMessage() });
        }
    }

    render() {
        const notes = this.state.notes;

        if (this.state.loading) {
            return (<Modal show={this.props.isOpen}>
                <Modal.Body>
                    <Loading />
                </Modal.Body>
            </Modal>)
        }

        return (
            <Modal show={this.props.isOpen}>
                <Modal.Header>
                    <div className="type">Staging</div>
                    <div className="description">Change Stage</div>
                </Modal.Header>
                <Modal.Body>
                    <Errors errors={this.state.errors} />

                    <div className="inner-row">
                        <div className="label">
                            Current Stage
                                        </div>
                        <i className="fa fa-question-circle helptext hidden"></i>
                        <div className="value">
                            {this.props.currentStage}
                        </div>
                    </div>
                    {/*this.state.removeMessage !== ''*/this.state.nextStageName.toLowerCase() == "withdrawn" ? <div className="box warning animate">{this.removeMessage()}</div> : null}
                    <Dropdown label="Next Stage" options={this.renderStageOptions()} name="Next Stage" selected={this.state.nextStage} onChange={this.onChangeStage} required={ValidationEnum.NotRequired} />
                    {this.state.nextStageName.toLowerCase() == "out for signature" ? <OutForSignature contacts={this.props.contactsData}
                        requests={this.props.packetRequests} packetId={this.props.packet.PacketDetail.Id} lastPrintDate={this.props.lastPrintDate}
                        onSignerChange={this.onSignerChange} nextStageId={this.state.nextStage} tmRepository={this.props.tmRepository} /> : null}
                    {this.state.nextStageName.toLowerCase() !== "withdrawn"?<div>
                        <Textarea label="Stage Notes" helpText="Stage notes are cumulative for all stages" text={notes} required={ValidationEnum.NotRequired} name="notes" onChange={this.onNotesChange} />
                    </div>:null}
                    
                </Modal.Body>
                <Modal.Footer>
                    <div className="notification-info">
                        {this.props.stageStepInfo}
                    </div>
                    <Button className="btn" onClick={this.onSaving} disabled={this.state.saving || this.props.stages.length === 0}>
                        {this.state.saving ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : null}
                        {" "}
                        {this.state.nextStageName.toLowerCase() == "withdrawn" ? "Continue removing product" :"Continue to Next Stage"}</Button>
                    <Button className="btn" onClick={this.props.onCancel} disabled={this.state.saving}>Close</Button></Modal.Footer>
            </Modal>
        );
    }
}

export default StageModal;


