import * as React from 'react';
import { IApproval, IApprovalInstance, IApprovalStep, IApprovalSubmitResponse } from '../api/IApi';
import "../style/approval.less";
import * as moment from "moment";
import * as fa from "./common/FontAwesome";

interface IProps {
    approvalInfo: IApproval;
    isLoaded: boolean;
    isApprovalLoaded: boolean;
    onSubmit(action: string): void;
    approvalError: IApprovalSubmitResponse;
}

interface IState {
    approvalClicked:boolean
}


class Approval extends React.Component<IProps, IState>{

    constructor(props: IProps) {
        super(props);
        this.state = {
            approvalClicked: false
        }
    }

    onClick = async () => {
        this.setState({ approvalClicked: true });
        await this.props.onSubmit(this.props.approvalInfo.Action);
        this.setState({ approvalClicked: false });
    }

    render() {
        
        if (this.props.approvalInfo === undefined) return null;
        if (this.props.approvalInfo && !this.props.approvalInfo.Name) return null;
        const processItems = this.props.approvalInfo.ProcessItems;
        const approvalStatus = !this.props.isApprovalLoaded && this.state.approvalClicked;

        return (
            this.props.isLoaded ?
                <div id="approval" className="box">
                    <div className="header">
                        <div>
                            Approval Process
                    </div>
                    </div>
                    {this.props.approvalInfo && this.props.approvalInfo.Name?<div className="approval-btn-layout">
                        <button disabled={approvalStatus} className="btn" type="button" onClick={this.onClick}>
                            {this.props.approvalInfo.ActionText}&nbsp;{approvalStatus ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : null}
                        </button>
                    </div>:null}
                    <div className={`approval-message-error-${this.props.approvalError ? this.props.approvalError.isSuccess:''}`}>
                        {this.props.approvalError !== undefined && this.props.approvalError !== null?this.props.approvalError.errors:""}
                    </div>
                    <div className="body">
                        <table className="table">
                            <thead>
                                <tr className="headerRow">
                                    <th>Action</th>
                                    <th>Date</th>
                                    <th>Status</th>
                                    <th>AssignedTo</th>
                                    <th>Actual Approver</th>
                                    <th>Comments</th>
                                    <th>Overall Status</th>
                                </tr>
                            </thead>

                            {processItems === null || processItems === undefined ?null:
                                processItems.map((x: IApprovalInstance, i: number) => {
                                    return <tbody> <tr key={i} className={`dataRow step-color`}>
                                        <td colSpan={6}>{x.StepTitle}</td>
                                        <td><span className={x.OverallStatus}>{x.OverallStatus}</span></td>
                                    </tr>
                                        {
                                            x.steps.map((y: IApprovalStep, z: number) => {
                                                return <tr key={z} className="dataRow">
                                                    <td>{y.LinkToApproval !== '' ? <span><a href={y.LinkToReassign}>Reassign&nbsp;</a>|<a href={y.LinkToApproval}>&nbsp;Approve / Reject</a></span> : ''}</td>
                                                    <td>{moment(y.CreatedDate).format("MM-DD-YYYY hh:mm A")}</td>
                                                    <td>{y.State}</td>
                                                    <td>{y.OriginalApproverName}</td>
                                                    <td>{y.ApproverName}</td>
                                                    <td>{y.Comments}</td>
                                                    <td></td>
                                                </tr>
                                            })
                                        }

                                    </tbody>

                                })
                            }


                        </table>
                    </div>
                </div> : <i className="fa fa-question-circle helptext hidden"></i>
        )
    }
}

export default Approval;


