import * as React from 'react';
import HelpText from './HelpText';
import { ValidationEnum } from "./InputField";

const Textbox = ({ label, name, text, onChange, isNumeric, helpText, required, readOnly }: { label: any, name: any, text: any, onChange: any, isNumeric: boolean, helpText?: string | undefined, required: ValidationEnum, readOnly?: boolean }) => {
    text = text == undefined ? "" : text;
    name = name == undefined ? "" : name;
    label = label == undefined ? "" : label;
    readOnly = readOnly == undefined ? false : readOnly;

    let cssClass = "";
    if (required === ValidationEnum.Correlated) {
        cssClass = "correlated-text";
    }
    else if (required === ValidationEnum.ImmediateRequired) {
        cssClass = "required-now-text";
    } else if (required === ValidationEnum.FutureRequired) {
        cssClass = "required-future-text";
    }

    function localOnChange(e: any) {
        if (isNumeric) {
            if (isNumericAmount(e.target.value))
                onChange(e);
        }
        else
            onChange(e);
    }
    const readOnlyClass = readOnly ? "readonly" : "";


    function isNumericAmount(textToTest: any) {
        //1st part checks for a number that can be from 0 - infinity: \d*
        //2nd part checks for a period that can be from 0 - 1 only: \.?
        //3rd part checks for a number after the period for 0 - 5 length: \d{0,5}
        return /^\d*\.?\d{0,5}$/g.test(textToTest);
    }

    return (
        <span className={"inner-row " + cssClass}>
            <div className={"label " + readOnlyClass}>
                <label htmlFor={name}>
                    {label}
                </label>
            </div>
            <HelpText helpText={helpText} />
            <div className="value">
                <input readOnly={readOnly} type="text" className='form-control' onChange={localOnChange} id={name} name={name} value={text} />
            </div>
        </span>
    );
}

export default Textbox;


