import * as React from "react";
import * as fa from "./FontAwesome";

const Loading =() => <div className="text-center" style={{padding: "20px"}}>
                    <i className={fa.combine(fa.faSpin, fa.faCircleONotch, fa.fa2X)}></i>
</div>;

export default Loading;


