import * as React from 'react';
import InputField, { ValidationEnum } from "./InputField"
import { KeyboardEvent, FocusEvent } from 'react';
import HelpText from './HelpText';

interface IMultiPillsState {
    pills: string[];
    label: string;
    name: string;
}

interface IMultiPillsProps {
    onChange: Function;
    name: string;
    label: string;
    pills: string;
    required: ValidationEnum;
    helpText?: string | undefined;
    readOnly?: boolean;
}

class MultiPills extends React.Component<IMultiPillsProps, IMultiPillsState> {
    constructor() {
        super();
        this.state = {
            pills: [],
            label: "",
            name: "",
        }
        this.initState = this.initState.bind(this);
        this.onKeyPress = this.onKeyPress.bind(this);
        this.removePill = this.removePill.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }

    componentWillMount() {
        this.initState(this.props);
    }

    componentWillReceiveProps(nextProps: IMultiPillsProps) {
        this.initState(nextProps);
    }

    initState(props: IMultiPillsProps) {
        const name = props.name == undefined ? "" : props.name;
        const label = props.label == undefined ? "" : props.label;
        let pills: string[] = [];
        if (props.pills !== undefined && props.pills.length > 0) {
            pills = props.pills.split(";").filter(x => x.length > 0).map((x: string) => {
                return x;
            });
        }
        this.setState({ name, label, pills });
    }

    onKeyPress(event: KeyboardEvent<HTMLInputElement>) {
        if (this.props.readOnly) return;

        if (event.key === 'Enter') {
            const value = event.currentTarget.value;
            this.insertNewPill(value);
            event.currentTarget.value = "";
            event.currentTarget.focus();
        }
    }

    onBlur(event: FocusEvent<HTMLInputElement>) {
        const value = event.currentTarget.value;
        this.insertNewPill(value);
        event.currentTarget.value = "";
    }

    insertNewPill(value: string) {
        const pills = this.state.pills;
        pills.push(...value.split(";").filter(x => x.length > 0));
        this.setState({ pills });
        const returnString = pills.join(";");
        const data = {
            target: {
                name: this.state.name,
                value: returnString
            }
        }
        this.props.onChange(data);
    }

    removePill(i: number) {
        const pills = this.state.pills;
        pills.splice(i, 1);
        this.setState({ pills });
        let returnString = pills.join(";");
        if (pills.length === 0)
            returnString = "";
        const data = {
            target: {
                name: this.state.name,
                value: returnString
            }
        }
        this.props.onChange(data);
    }


    render() {
        const readOnly = this.props.readOnly === undefined ? false : this.props.readOnly;
        const required: ValidationEnum = 1;
        let cssClass = "";
        const readOnlyClass = readOnly ? "readonly" : "";

        if (required === ValidationEnum.Correlated) {
            cssClass = "correlated";
        } else if (required === ValidationEnum.ImmediateRequired) {
            cssClass = "required-now";
        } else if (required === ValidationEnum.FutureRequired) {
            cssClass = "required-future";
        }
        if (this.state.pills.length === 0) {
            cssClass += "-text";
        }
        return (<div className={"inner-row " + (cssClass)}>
            <div className={"label " + readOnlyClass}>
                <label htmlFor={this.state.name}>
                    {this.props.label}
                </label>
            </div>
            <HelpText helpText={this.props.helpText} />
            <div className="value multi-pills">
                {this.state.pills.map((x: string, i: number) => {
                    return <div className="pill" key={i}>{x} <i className="close" onClick={() => { this.removePill(i) }}>&times;</i></div>;
                })}
                <input readOnly={readOnly} placeholder={this.state.pills.length === 0 ? "" : "Add Additional Items"} onBlur={this.onBlur} type="text" onKeyUp={this.onKeyPress} id={this.state.name} name={this.state.name} />
            </div>
        </div>
        );
    }

}

export default MultiPills;



