import * as React from "react";
import ReactDatepicker from "react-datepicker";
import * as moment from "moment";
import "react-datepicker/dist/react-datepicker.css";

interface IDatepicker {
    label: string,
    name: string,
    data: string,
    onChange: any,
    readOnly?: boolean;
}

class DatePicker extends React.Component<IDatepicker, any> {
    constructor(props) {
        super(props);
        this.state = { value: "" };

        this.onDateChange = this.onDateChange.bind(this);
    }

    componentWillReceiveProps(nextProps): void {
        this.setState({ value: nextProps.data });
    }

    componentWillMount(): void {
        this.setState({ value: this.props.data });
    }

    onDateChange(date: moment.Moment) {
        const target = {
            name: this.props.name,
            value: date !== null ? date.utc().valueOf() : "",
        }
        this.props.onChange({ target });
    }



    render() {
        const label = this.props.label;
        const date = this.state.value !== undefined && this.state.value.length > 0 ? moment(this.state.value) : null;
        const value = this.state.value !== undefined ? moment(this.state.value).utc().format("L") : undefined;
        const readOnly = this.props.readOnly === undefined ? false : this.props.readOnly;
        const readOnlyClass = readOnly ? "readonly" : "";
        return (
            <span className="inner-row">
                <div className={"label " + readOnlyClass}>
                    <label htmlFor={name}>
                        {label}
                    </label>
                </div>
                <i className="fa fa-question-circle helptext hidden"></i>
                <div className="value">
                    {readOnly ? value :
                        <ReactDatepicker className="form-control" onChange={this.onDateChange} selected={date} value={value} dateFormat="L" showTimeSelect={false}></ReactDatepicker>
                    }
                </div>
            </span>
        );
    }

    dateTimePicker;
};


export default DatePicker;


