
declare var LogStateSetting: string;

export enum LogState {
    Error = 0,
    Debug = 1,
    Info = 2
}

class Logger {
    private _logStateString: string[] = ["Error"];
    constructor() {
        if (typeof (LogStateSetting) != "undefined")
            this._logStateString = LogStateSetting.split(',');

        console.log("Tm Boarding logging state(s): ", this._logStateString);
    }

    log(logState: LogState, message?: any, ...optionalParams: any[]) {
        if (this._logStateString.find((x: string) => x === "Info") !== undefined && logState === LogState.Info)
            this.logNormal(message, ...optionalParams);

        if (this._logStateString.find((x: string) => x === "Debug") !== undefined && logState === LogState.Debug)
            this.logDebug(message, ...optionalParams);

        if (this._logStateString.find((x: string) => x === "Error") !== undefined && logState === LogState.Error)
            this.logError(message, ...optionalParams);
    }

    private logNormal(message?: any, ...optionalParams: any[]) {
        if (optionalParams.length == 0)
            console.log(message);
        else
            console.log(message, ...optionalParams);
    }

    private logDebug(message?: any, ...optionalParams: any[]) {
        if (optionalParams.length == 0) {
            console.groupCollapsed("Debug");
            console.log(message);
            console.trace();
            console.groupEnd();
        }
        else {
            console.groupCollapsed("Debug: " + message);
            console.trace(...optionalParams);
            console.groupEnd();
        }
    }

    private logError(message?: any, ...optionalParams: any[]) {
        if (optionalParams.length == 0) {
            console.groupCollapsed("Error");
            console.log(message);
            console.error();
            console.groupEnd();
        }
        else {
            console.groupCollapsed("Error: " + message);
            console.error(...optionalParams);
            console.groupEnd();
        }
    }

}


const logger = new Logger();

export default logger;


