import * as React from 'react';
import * as fa from './FontAwesome';
import HelpText from './HelpText';

interface IMultiPicklistForSectionProps {
    data: any,
    fields: IValues[],
    isView: boolean,
    label: string,
    onChange: any,
    helpText?: string | undefined;
}

interface IMultiPicklistForsectionState {
    isListVisible: boolean,
    scrollAdded: boolean,
}

interface IValues {
    Label: string,
    Name: string,
    ObjectId: string,
}

class MultiPicklistForSection extends React.Component<IMultiPicklistForSectionProps, IMultiPicklistForsectionState> {

    constructor() {
        super();
        this.state = {
            isListVisible: false,
            scrollAdded: false
        }
        this.togglePicklist = this.togglePicklist.bind(this);
        this.resize = this.resize.bind(this);
    }

    resize(itm: Element) {
        if (itm != null) {
            if (!this.state.scrollAdded) {
                this.setState({ scrollAdded: true });
                itm.addEventListener("mousewheel",
                    (x: any) => {
                        if ((itm.scrollTop <= 0 && x.wheelDelta > 0) ||
                            (itm.scrollHeight - itm.scrollTop <= itm.clientHeight && x.wheelDelta < 0))
                            x.preventDefault();
                    });
            }
            const style = `width:${this.selectItem.offsetWidth}px;margin-top:${this.selectItem.offsetHeight}px`;
            itm.setAttribute("style", style);
        }
    }

    togglePicklist() {
        this.setState({ isListVisible: !this.state.isListVisible });
    }

    componentDidMount() {
        document.addEventListener("click",
            (e: any) => {
                let hide = true;
                e.path.forEach((x: any) => {
                    if (x === this.multiPicklist)
                        hide = false;
                });
                if (hide) {
                    this.setState({ isListVisible: false });
                }
            });
    }

    onChange(selected: boolean, item: IValues) {
        const fields: any[] = this.props.data;
        fields[item.Name] = selected;
        this.forceUpdate();
        this.props.onChange();
    }

    selectItem: any;
    pickListDiv: any;
    multiPicklist: any;

    render() {
        var selectedItems: IValues[] = [];
        this.props.fields.forEach((x: IValues) => {
            if (this.props.data[x.Name]) {
                selectedItems.push(x);
            }
        });

        return (
            <span className="inner-row">
                <div className="label">
                    <label>
                        {this.props.label}
                    </label>
                </div>
                <HelpText helpText={this.props.helpText} />
                <div className="value">
                    <div className="multi-picklist" ref={(x: any) => this.multiPicklist = x}>
                        {!this.props.isView
                            ? <div style={{ width: "100%" }}>
                                <div ref={(x: any) => this.selectItem = x}>
                                    <div className="input">
                                        {selectedItems.length === 0 ? "No Items Selected" : null}
                                        {selectedItems.map((x: IValues) => {
                                            return (<span key={x.Name}><i className={fa.faClose} onClick={() => {
                                                this.onChange(false, x);
                                                this.setState({ isListVisible: false });
                                            }}></i> {x.Label}</span>);
                                        })}
                                    </div>
                                    <div className="arrow" onClick={this.togglePicklist}>
                                        <span className={fa.faAngleDown}></span>
                                    </div>
                                </div>
                                {this.state.isListVisible
                                    ? <div className="picklist" ref={(x: any) => this.resize(x)}>
                                        {this.props.fields.map((x: IValues) => {
                                            const checked = this.props.data[x.Name];
                                            return (<label key={x.Name}>
                                                <input checked={checked} onChange={(e: any) => this.onChange(!checked,
                                                    x)
                                                } type="checkbox" value={x.Name} />
                                                <span>{x.Label}</span>
                                            </label>);
                                        })}
                                    </div>
                                    : null
                                }
                            </div>
                            : selectedItems.map((x: IValues) => { return x.Label }).join(", ")
                        }
                    </div>
                </div>
            </span>
        );
    }

}

export default MultiPicklistForSection;


