import * as React from 'react';
import HelpText from './HelpText';
import { ValidationEnum } from "./InputField";

export interface IOption {
    Value: string;
    Label: string;
    Enabled?: boolean;
}

const Dropdown =
    ({ label, name, options, selected, onChange, helpText, required, disabled, readOnly }:
        {
            label: any,
            name: any,
            options: IOption[],
            selected: any,
            onChange: any,
            helpText?: string | undefined,
            required: ValidationEnum,
            disabled?: boolean,
            readOnly?: boolean
        }) => {

        name = name == undefined ? "" : name;
        label = label == undefined ? "" : label;
        selected = selected == undefined ? options[0] : selected;
        disabled = disabled == undefined ? false : disabled;
        readOnly = readOnly == undefined ? false : readOnly;
        let cssClass = "";
        if (required === ValidationEnum.Correlated) {
            cssClass = "correlated-text";
        }
        else if (required === ValidationEnum.ImmediateRequired) {
            cssClass = "required-now-text";
        } else if (required === ValidationEnum.FutureRequired) {
            cssClass = "required-future-text";
        }
        const readOnlyClass = readOnly ? "readonly" : "";

        
        return (
            <span className={"inner-row " + cssClass}>
                <div className={"label " + readOnlyClass}>
                    <label htmlFor={name}>
                        {label}
                    </label>
                </div>
                <HelpText helpText={helpText} />
                <div className="value">
                    <select readOnly={readOnly} value={selected} className="form-control" onChange={onChange} id={name} name={name} disabled={disabled}>
                        {options.map((x: IOption, index: number) => {
                            return (<option value={x.Value} key={index} disabled={x.Enabled != undefined ? !x.Enabled : false}>{x.Label}</option>);
                        })}
                    </select>
                </div>
            </span>
        );
    }
export default Dropdown;


