import * as React from 'react';
import * as fa from './FontAwesome';

const HelpText = ({ helpText }: { helpText?: string | undefined }) => {
    if (helpText === undefined || helpText === null || helpText.length === 0)
        return <i className={fa.faQuestionCircle + " helptext hidden"}></i>;
    return <i className={fa.faQuestionCircle + " helptext"} title={helpText}></i>;
}

export default HelpText;


