import * as React from "react";
import * as fa from "../common/FontAwesome"
import * as numeral from 'numeral';

const table = ({ children }: { children: any }) =>
{
    return (
        <table className="table mb20">
            {children}
        </table>
    );
}

const tableBody = ({ data, headers, onEdit, onSelect, onChange, isView, showEdit, handleSelectAll, loadingId }: { data: any, headers: any, onEdit: any, onSelect: any, onChange: any, isView: boolean, showEdit: boolean, handleSelectAll: any, loadingId: string }) =>
{
    function toCurrency(currencyObj: string)
    {
        const number = numeral(currencyObj);
        numeral.defaultFormat('$0,0.00');
        return number.format();
    }

    function returnObject(obj: any, pm: any, index: number)
    {
        if (/text|numeric|currency|phone/gi.test(obj.InputType) && isView)
        {
            if (/currency/gi.test(obj.InputType))
                return toCurrency(pm[obj.Name]);
            else if (/phone/gi.test(obj.InputType))
            {
                if (pm[obj.Name] === undefined)
                    return "";
                let phone = pm[obj.Name] !== null ? (pm[obj.Name]).replace(/[^\d]/gi, "") : "";
                phone = phone.replace(/(\d{3})(\d{3})(\d{4})/gi, "($1) $2-$3");
                return phone;
            } else
                return (pm[obj.Name]);
        } else if (/checkbox/gi.test(obj.InputType))
        {
            pm[obj.Name] = pm[obj.Name] == undefined ? false : pm[obj.Name];
            return (<div className='align-center'><input type='checkbox' onChange={onChange} disabled={isView} name={obj.Name} data-index={index} checked={pm[obj.Name]} /></div>);
        } else if (/dropdown/gi.test(obj.InputType) && isView)
        {
            return (pm[obj.Name]);
        } else if (/dropdown/gi.test(obj.InputType))
        {
            return <select name={obj.Name} onChange={onChange} className="form-control">
                {obj.options.map(x =>
                {
                    return <option value={x.Value} selected={pm[obj.Name] === x.Value}>{x.Label}</option>;
                })}
            </select>;
        }
        return null;
    }

    const renderHeader = () =>
    {
        return headers.map((hdr: any) =>
        {
            return hdr.Fields.map((x: any, index: number) =>
            {
                return (<th className={/checkbox/gi.test(x.InputType) ? 'align-center' : ''} key={index}>
                    {x.Label}
                </th>);
            });
        });
    }

    function handleSelect(obj, index)
    {
        let event = {
            target: {
                checked: !obj.Included.Selected,
                name: obj.Included.Id,
                index: index
            }
        };
        onSelect(event);
    }

    function hasRows()
    {
        if (data != undefined)
        {
            return data.filter(x => Object.getOwnPropertyNames(x).length !== 0).length > 0;
        }
        return false;
    }

    const renderBody = () =>
    {
        if (data != undefined)
        {
            return data.filter(x => Object.getOwnPropertyNames(x).length !== 0).map((x: any, index: number) =>
            {
                return (
                    <tr className="dataRow" key={index}>
                        {isView && showEdit
                            ? <td>
                                <a href="javascript:void(0)" onClick={() => { onEdit(x.Included.Id) }}>
                                    {loadingId === x.Included.Id ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : <i className={fa.faEdit}></i>} {" "}
                                    Edit</a>
                            </td>
                            : null}
                        {isView
                            ? <td className='align-center' onClick={() => { showEdit && handleSelect(x, index) }}><input type='checkbox' disabled={!showEdit} name={x.Included.Id} onClick={(e) =>
                            {
                                handleSelect(x, index); e.stopPropagation();
                            }
                            } checked={x.Included.Selected} /></td>
                            : <td>{x.RelatedName}</td>}

                        {headers.map((hdr: any) =>
                        {
                            return hdr.Fields.map((y: any, index2: number) =>
                            {
                                return (<td key={index2}>
                                    {returnObject(y, x, index)}
                                </td>);
                            });
                        })}
                    </tr>
                );
            });
        }
        return null;
    }

    if (headers == undefined && data == undefined)
        return null;
    return (
        <tbody>{hasRows() ?
            <tr className="headerRow">
                {isView && onEdit != null && showEdit ? <th className="align-center" style={{ width: '60px' }}></th> : null}
                {isView ? <th className="align-center" style={{ width: "60px" }}>{showEdit ? <input type="checkbox" onClick={handleSelectAll} title="Toggle Select All" /> : null} </th> : <th style={{ width: "150px" }}>Name</th>}
                {renderHeader()}
            </tr> : null}
            {renderBody()}
        </tbody>
    );
}

export { table as Table, tableBody as TableBody }



