import * as React from 'react';
import * as fa from '../common/FontAwesome';
import Link from '../common/Link';
import * as PropTypes from 'prop-types';

interface INavProps {
    packet: any,
    onAddNew: any,
    movingStage: boolean;
    navigateTo: (href: string) => void;
}

interface INavState {
    packet: any,
    bodyWidth: number,
    hamburgerOpen: boolean,
}

class Nav extends React.Component<INavProps, INavState> {
    constructor() {
        super();
        this.state = {
            packet: undefined,
            bodyWidth: 0,
            hamburgerOpen: false
        }
        this.toggleHamburger = this.toggleHamburger.bind(this);
    }

    componentDidMount() {
        window.addEventListener("resize",
            () => {
                this.setState({ bodyWidth: window.document.body.clientWidth }, () => {
                    if (this.state.bodyWidth > 750 && this.state.hamburgerOpen)
                        this.setState({ hamburgerOpen: false });
                });
            });
        this.setState({ bodyWidth: window.document.body.clientWidth });
    }

    componentWillReceiveProps(nextProps: any) {
        this.setState({ packet: nextProps.packet });
    }

    componentWillMount() {
        this.setState({ packet: this.props.packet });
    }

    toggleHamburger() {
        this.setState({ hamburgerOpen: !this.state.hamburgerOpen });
    }

    linkTo(href: string) {
        this.props.navigateTo(href);
    }

    render() {
        if (this.state.packet["Requests"] === undefined)
            return null;
        const hamburgerIcon = this.state.hamburgerOpen ? fa.faClose : fa.faBars;


        return (<div className="box">
            <div className="header"><div>Products in this Packet</div>
                {this.state.bodyWidth <= 750 ?
                    <div className="hamburger" onClick={this.toggleHamburger}>
                        <i className={fa.combine(fa.fa2X, hamburgerIcon)} />
                    </div> : null}
            </div>
            {(this.state.packet.Requests != undefined && this.state.bodyWidth > 750) ||
                (this.state.packet.Requests != undefined && this.state.hamburgerOpen && this.state.bodyWidth <= 750) ?
                <table className="nav">
                    <thead>
                        <tr>
                            {this.state.packet.Requests.map((x: any, i: any) => {
                                var href = location.origin + "/" + x.Id + (location.href.indexOf("TmBoardingEdit") > -1 ? "/e" : "");
                                return (<td onClick={() => {this.linkTo(href)}} className={x.Id === this.state.packet.Request.Id ? "active" : ""} key={i}>
                                    <span style={{ cursor: "pointer" }}>{x.Name}</span>
                                    <div className="stage visible-on-small">{this.props.movingStage && x.Id === this.state.packet.Request.Id ? <i style={{ color: "black" }} className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : "(" + x.Stage + ")"}</div>
                                </td>);
                            })}
                            <td>
                                <a href="javascript:void(0)" onClick={this.props.onAddNew}><i className={fa.faPlusCircle}></i> Add Product</a>
                            </td>
                        </tr>
                    </thead>
                    <tbody className="hide-on-small">
                        <tr>
                            {this.state.packet.Requests.map((x: any, i: any) => {
                                var href = location.origin + "/" + x.Id + (location.href.indexOf("TmBoardingEdit") > -1 ? "/e" : "");
                                return (<td onClick={() => {this.linkTo(href)}} className={x.Id === this.state.packet.Request.Id ? "active" : ""} key={i}>
                                    <div className="stage">{this.props.movingStage && x.Id === this.state.packet.Request.Id ? <i style={{ color: "black" }} className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : "(" + x.Stage + ")"}</div>
                                </td>);
                            })}
                            <td>
                                <div className="stage">&nbsp;</div>
                            </td>
                        </tr>
                    </tbody>
                </table> : null}
        </div>);
    }
}


export default Nav;


