import * as React from "react";
import * as PropTypes from 'prop-types';

interface ILink
{
    to: any,
    openInNewTab?: boolean;
    className?: string;
    disabled?: boolean;
}

class Link extends React.Component<ILink, any> {
    constructor()
    {
        super();
        this.onClick = this.onClick.bind(this);
    }

    onClick()
    {
        this.context.to(this.props.to);
    }

    render()
    {
        return (
            <a className={this.props.className} target={this.props.openInNewTab && this.props.openInNewTab === true ? "_blank" : "_self"} href="javascript:void(0)" data-to={this.props.to} onClick={this.onClick}>{this.props.children}</a>
        );
    }

    static contextTypes: { to };
}

Link.contextTypes = {
    to: PropTypes.func
}

export default Link;


