import * as React from 'react';
import Modal from "./common/Modal";
import { ITMRepository, IValidationResult } from '../api/TMRepository';
import Button from "./common/Button";
import { IStages } from '../api/IApi';

interface IValidationRequestModalProps {
    tmRepository: ITMRepository;
    header: string;
}

interface IValidationRequestModalState {
    show: boolean;
    validations: IValidationResult[];
    IsLoading: boolean;
}

class ValidationRequestModal extends React.Component<IValidationRequestModalProps, IValidationRequestModalState> {
    private readonly _tmRepository: ITMRepository;

    constructor(props: IValidationRequestModalProps, context: any) {
        super(props, context);
        this._tmRepository = props.tmRepository;

        this.state = {
            show: false,
            validations: [],
            IsLoading: false
        }

        this.closeModal = this.closeModal.bind(this);
        this.openModal = this.openModal.bind(this);
    }

    public openModal() {
        this.setState({ show: true, IsLoading: true }, () => {
            this._tmRepository.getAllStages().then((x: IStages[]) => {
                const stages = x.map((y: IStages) => { return y.Id });

                this._tmRepository.validateRequest(stages).then((x: IValidationResult[]) => {
                    this.setState({ validations: x , IsLoading:false});
                });
            });
        });
    }

    private closeModal() {
        this.setState({ show: false, IsLoading: false});
    }

    private getValidResponse(x: IValidationResult, n: number) {
        return (
            <div key={n} className="validation-modal-section">
                <div className="stage">Stage: {x.Stage}</div>
                {x.Errors.map((y: string, i: number) => {
                    return <div className="error" key={i}>{y}</div>
                })}
            </div>

        );
    }

    render() {
        return (
            <Modal show={this.state.show}>
                <Modal.Header>{this.props.header}</Modal.Header>
                <Modal.Body>
                    {this.state.IsLoading ? "Loading ..." :                       
                            this.state.validations === null || this.state.validations.length === 0 ? "All stages valid" : this.state.validations.map((x: IValidationResult, i: number) => {
                                return this.getValidResponse(x, i);
                            })
                        }
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={this.closeModal}>Close</Button>
                </Modal.Footer>
            </Modal>
        );
    }
}

export default ValidationRequestModal;



