import * as React from "react";

interface IModal {
    show: boolean;
}

class Modal extends React.Component<IModal, any> {
    constructor() {
        super();
    }

    componentWillReceiveProps(nextProps: IModal): void {
        if (this.myModal != null) {
            if (nextProps.show) {
                this.myModal.classList.add("in");
                this.myBackdrop.classList.add("in");
            } else {
                this.myModal.classList.remove("in");
                this.myBackdrop.classList.remove("in");
            }
        }
    }

    render() {
        return (<div>
            <div ref={c => this.myBackdrop = c} className="ebt-modal-backdrop">
                <div ref={c => this.myModal = c} className="ebt-modal">
                    {this.props.children}
                </div>
            </div>
        </div>);
    }

    public static Title;
    public static Header;
    public static Body;
    public static Footer;

    myModal;
    myBackdrop;
}


const title = ({ title }) => { return (<div className="ebt-modal-title">{title}</div>) };
const body = ({ children }) => { return (<div className="ebt-modal-body">{children}</div>) };
const header = ({ children }) => { return (<div className="ebt-modal-header">{children}</div>) };
const footer = ({ children }) => { return (<div className="ebt-modal-footer">{children}</div>) };

Modal.Title = title;
Modal.Header = header;
Modal.Body = body;
Modal.Footer = footer;

export default Modal;


