import * as React from 'react';

interface ILazyLoadProps
{
    load: () => Promise<any>;
    delayMS?: number;
}

interface ILazyLoadState
{
    child: any | null;
    isLoading: boolean;
    timedOut: boolean;
}

export function lazyLoad<P>(opts: ILazyLoadProps)
{
    const options: ILazyLoadProps =
        Object.assign({
            delayMS: 200
        }, opts);

    return class LazyLoadComponent extends React.Component<P, ILazyLoadState> {
        private _timeout: number = -1;

        constructor(props: any)
        {
            super(props);
            this.state = {
                child: null,
                isLoading: true,
                timedOut: false
            }
        }

        componentWillMount()
        {
            const timeout = setTimeout(() =>
            {
                this.setState({ timedOut: true });
            },
                options.delayMS);

            const loadResult = options.load();

            loadResult.then(result =>
            {
                this.setState({ child: result["default"], isLoading: false });
                clearTimeout(timeout);
            });
        }

        render()
        {
            if (this.state.isLoading && !this.state.timedOut)
                return null;

            if (this.state.isLoading && this.state.timedOut)
                return <div>Loading...</div>;

            if (this.state.child)
                return <this.state.child {...this.props} />

            return null;
        }


    }
}


