import * as React from 'react';
import HelpText from './HelpText';
import { ValidationEnum } from "./InputField";

const RadioButton = ({ label, name, checked, onChange, disabled, helpText, id, required, readOnly }: { label: any, name: any, checked: any, onChange: any, disabled: boolean, helpText?: string | undefined, id?: string, required: ValidationEnum, readOnly?: boolean }) => {
    name = name == undefined ? "" : name;
    label = label == undefined ? "" : label;
    checked = checked == undefined ? false : checked;
    readOnly = readOnly == undefined ? false : readOnly;
    let cssClass = "";
    if (required === ValidationEnum.Correlated) {
        cssClass = "correlated";
    } else if (required === ValidationEnum.ImmediateRequired) {
        cssClass = "required-now";
    } else if (required === ValidationEnum.FutureRequired) {
        cssClass = "required-future";
    }
    const readOnlyClass = readOnly ? "readonly" : "";


    return (
        <div className={"inner-row " + cssClass}>
            <div className={"label " + readOnlyClass}>
                <label htmlFor={id !== undefined ? id : name}>
                    {label}
                </label>
            </div>
        <HelpText helpText={helpText} />
        <div className="value">
                <input type="radio" readOnly={readOnly} onChange={onChange} id={id !== undefined ? id : name} name={name} checked={checked
                } disabled={disabled} />
            </div>
        </div>
    );
}


export default RadioButton;


