    private readonly _dataBuilder: IDataBuilder;
    private readonly _pdfGenerator: IPdfGenerator;
    private readonly _htmlGenerator: IHtmlGenerator;
    private readonly _miscFuncs: IMiscFunctions;
    private readonly _saveHelper: ISaveHelper;
    private _validationRequestModal: ValidationRequestModal | null;
    private _loaded: boolean;
    private _approvalLoaded: boolean;
    private readonly STAGETYPE_ARCHIVE = "Archive";
    private readonly STAGETYPE_ALLVALIDATIONSREQUIRED = "AllValidationRequired";
    private readonly STAGETYPE_END = "End";
    private readonly STAGETYPE_VIEWONLY = "ViewOnly";

