    onValidate(): Promise<boolean> {
        return new Promise((resolve, reject) => {
            let detailsObject = this._dataBuilder.buildPageData(this.state, "Edit");
            detailsObject = detailsObject.filter(x => x.FieldName !== "Selected" && (x.ValidateNow || x.Correlated) && x.FieldName.indexOf(".null") < 0);
            if (detailsObject !== null && detailsObject !== undefined && detailsObject.length > 0) {
                const objectName = detailsObject[0].FieldName.match(/^\w+\.?/g);
                const validationFields: any[] = [];
                for (let dO of detailsObject) {
                    if (objectName !== null) {
                        validationFields.push({
                            FieldName: dO.FieldName,
                            FieldValue: dO.FieldValue
                        });
                    }
                }
                const validationRequest = {
                    ObjectName: objectName !== null ? objectName[0].replace(".", "") : "",
                    StageId: this.state.stageDetails.Id,
                    Product: this.state.packet.Type,
                    RequestId: this.state.packet.Request.Id,
                    Fields: validationFields
                };
                this._tmRepository.getValidations(validationRequest).then(x => {
                    if (x === undefined || x === null) {
                        resolve(true);
                    } else {
                        const failed = x.filter(x => !x.IsValid);
                        const errors: string[] = [];
                        for (let f of failed) {
                            errors.push(f.Error);
                        }
                        this.setState({ errors },
                            () => {
                                if (failed.length > 0) {
                                    resolve(false);
                                } else {
                                    resolve(true);
                                }
                            });
                    }
                });
            } else {
                resolve(true);
            }
        });
    }

