import * as React from "react";
import * as fa from "./common/FontAwesome";
import Modal from "./common/Modal";
import Button from "./common/Button";
import Dropdown from './common/Dropdown';
import Errors from "./common/Errors";
import { ValidationEnum } from "./common/InputField";
import { ChangeEvent } from "react";

interface IPrintModalProps
{
    isOpen: boolean;
    onSave: (includeMasterAgreement: boolean) => void;
    onCancel: () => void;
    printButtonText: string;
    lastPrintDate?: number;
}

interface IPrintModalState
{
    printMasterAgreement: boolean;
    isPrinting: boolean;
}

class PrintModal extends React.Component<IPrintModalProps, IPrintModalState> {
    constructor()
    {
        super();
        this.state = {
            printMasterAgreement: false,
            isPrinting: false
        }
    }

    componentWillReceiveProps(nextProps: IPrintModalProps)
    {
        if (!nextProps.isOpen)
            this.setState({ isPrinting: false, printMasterAgreement: false });
    }

    onMasterAgreementChange = (e: ChangeEvent<HTMLInputElement>) =>
    {
        this.setState({ printMasterAgreement: e.target.checked });
    }

    onSave = () =>
    {
        this.setState({ isPrinting: true }, () =>
        {
            this.props.onSave(this.state.printMasterAgreement);
        })
    }

    render()
    {
        return (
            <Modal show={this.props.isOpen}>
                <Modal.Header>
                    <div className="type">Printing</div>
                    <div className="description">Print Details</div>
                </Modal.Header>
                <Modal.Body>
                    <label>
                        <input type="checkbox" checked={this.state.printMasterAgreement} onChange={this.onMasterAgreementChange}></input>
                        Include Master Agreement
                    </label>
                    <br /><br />
                    Last Signed Master Agreement: <b>{this.props.lastPrintDate === undefined ? "Never" : new Date(this.props.lastPrintDate).toLocaleDateString()}</b>
                </Modal.Body>
                <Modal.Footer>
                    <Button className="btn" onClick={this.onSave} disabled={this.state.isPrinting}>
                        {this.state.isPrinting ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : null}
                        {" "}
                        {this.props.printButtonText || "Print"}</Button>
                    <Button className="btn" onClick={this.props.onCancel} disabled={this.state.isPrinting}>Close</Button></Modal.Footer>
            </Modal>
        );
    }
}

export default PrintModal;


