import * as React from 'react';
import * as fa from './FontAwesome';
import HelpText from './HelpText';
import { ValidationEnum } from "./InputField";

interface IMultiPicklistProps {
    data: IValues[],
    isView: boolean,
    label: string,
    required: ValidationEnum;
    helpText?: string | undefined;
    readOnly?: boolean;
}

interface IMultiPicklistState {
    isListVisible: boolean,
    values: IValues[],
    scrollAdded: boolean,
}

interface IValues {
    label: string,
    value: string,
    selected: boolean,
}

class MultiPicklist extends React.Component<IMultiPicklistProps, IMultiPicklistState> {

    constructor() {
        super();
        this.state = {
            isListVisible: false,
            values: [],
            scrollAdded: false
        }
        this.togglePicklist = this.togglePicklist.bind(this);
        this.resize = this.resize.bind(this);
    }

    resize(itm: Element) {
        if (itm != null) {
            if (!this.state.scrollAdded) {
                this.setState({ scrollAdded: true });
                itm.addEventListener("mousewheel",
                    (x: any) => {
                        if ((itm.scrollTop <= 0 && x.wheelDelta > 0) ||
                            (itm.scrollHeight - itm.scrollTop <= itm.clientHeight && x.wheelDelta < 0))
                            x.preventDefault();
                    });
            }
            const style = `width:${this.selectItem.offsetWidth}px;margin-top:${this.selectItem.offsetHeight}px`;
            itm.setAttribute("style", style);
        }
    }

    togglePicklist() {
        this.setState({ isListVisible: !this.state.isListVisible });
    }

    componentDidMount() {
        this.setState({ values: this.props.data });
        document.addEventListener("click",
            (e: any) => {
                let hide = true;
                e.path.forEach((x: any) => {
                    if (x === this.multiPicklist)
                        hide = false;
                });
                if (hide) {
                    this.setState({ isListVisible: false });
                }
            });
    }

    onChange(select: any, item: IValues) {
        const values: any[] = this.state.values;
        const found: IValues = values.find((x: any) => x.value === item.value);
        found.selected = select.target.checked;
        this.setState({ values });
    }

    selectItem: any;
    pickListDiv: any;
    multiPicklist: any;

    render() {
        const readOnly = this.props.readOnly === undefined ? false : this.props.readOnly;
        const event = {
            target: {
                checked: false
            }
        };
        let cssClass = "";
        if (this.props.required === ValidationEnum.Correlated) {
            cssClass = "correlated";
        } else if (this.props.required === ValidationEnum.ImmediateRequired) {
            cssClass = "required-now";
        } else if (this.props.required === ValidationEnum.FutureRequired) {
            cssClass = "required-future";
        }
        const readOnlyClass = readOnly ? "readonly" : "";

        return (
            <span className={"inner-row " + cssClass}>
                <div className={"label text-middle text-right " + readOnlyClass}>
                    <label>
                        {this.props.label}
                    </label>
                </div>
                <HelpText helpText={this.props.helpText} />
                <div className="value">
                    <div className="multi-picklist" ref={(x: any) => this.multiPicklist = x}>
                        {!this.props.isView && !readOnly
                            ?
                            <div style={{ width: "100%" }}>
                                <div ref={(x: any) => this.selectItem = x}>
                                    <div className="input">
                                        {this.state.values.filter((x: IValues) => x.selected).length === 0
                                            ? "No Items Selected"
                                            : null}
                                        {this.state.values.filter((x: IValues) => x.selected).map((x: IValues) => {
                                            return (<span key={x.value}><i className={fa.faClose} onClick={() => {
                                                this.onChange(event, x);
                                                this.setState({ isListVisible: false });
                                            }}></i> {x.label}</span>);
                                        })}
                                    </div>
                                    <div className="arrow" onClick={this.togglePicklist}>
                                        <span className={fa.faAngleDown}></span>
                                    </div>
                                </div>
                                {this.state.isListVisible ?
                                    <div className="picklist" ref={(x: any) => this.resize(x)}>
                                        {this.state.values.map((x: IValues) => {
                                            return (<label key={x.value}>
                                                <input checked={x.selected} onChange={(e: any) => this.onChange(e, x)
                                                } type="checkbox" value={x.value} />
                                                <span>{x.label}</span>
                                            </label>);
                                        })}
                                    </div>
                                    :
                                    null
                                }
                            </div> :
                            this.state.values.filter((x: IValues) => x.selected).map((x: IValues, i: number) => {
                                return (x.label + (i + 1 !== this.state.values.filter((x: IValues) => x.selected).length ? ", " : ""));
                            })
                        }
                    </div>
                </div>
            </span>

        );
    }

}

export default MultiPicklist
export { IValues }



