import { ValidationButtonState } from "../components/common/pageValidator";
import TMRepository, { ITMRepository, IValidationResult } from '../api/TMRepository';
import * as _ from "../lodash";
import { Promise } from "es6-promise";

export interface IMiscFullValidation
{
    State: ValidationButtonState;
    Validations: IValidationResult[];
}

export interface IMiscFunctions
{
    RunAmIANewProduct(recordJson: any): void;
    RunFullValidation(currentStageId: string): Promise<IMiscFullValidation>;
}

class MiscFunctions implements IMiscFunctions
{
    private readonly _repo: ITMRepository;
    constructor(repo: ITMRepository)
    {
        this._repo = repo;
    }

    public RunAmIANewProduct(recordJson: any): void
    {
        var loc = window.location.href;
        if (loc.indexOf("save_new=1") > -1)
        {
            window.history.pushState("", document.title, window.location.origin + "/" + recordJson["Id"]);
        }
    }
    
    public RunFullValidation(currentStageId: string): Promise<IMiscFullValidation>
    {
        return new Promise((resolve, reject) =>
        {
            this._repo.getAllStages().then((x) =>
            {
                const stages = x.map(y =>
                {
                    return y.Id;
                });
                this._repo.validateRequest(stages).then((x: IValidationResult[]) =>
                {
                    let state = ValidationButtonState.Waiting;
                    let stagesWithError = x.map(y => { return y.StageId });

                    if (x.length > 0)
                    {
                        state = ValidationButtonState.IntermediateInvalid;
                        if (_.indexOf(stagesWithError, currentStageId) > -1)
                        {
                            state = ValidationButtonState.Invalid;
                        }
                    } else
                    {
                        state = ValidationButtonState.Valid;
                    }

                    resolve({
                        State: state,
                        Validations: x
                    } as IMiscFullValidation);

                });
            });
        });
    }

}

export default MiscFunctions;


