import * as React from 'react';
import { Table, TableBody } from "./Grid/Table";
import * as fa from "./common/FontAwesome";
import Button from "./common/Button";
import Loading from "./common/Loading";
declare var recordJson: any;

enum Type {
    Edit,
    View
}

const grid = ({ data, title, type, onEdit, onSelect, onAdd, onChange, showEdit, handleSelectAll, loadingId }: { data: any, title: string, type: Type, onEdit: any, onSelect: any, onAdd: any, onChange: any, showEdit: boolean, handleSelectAll: any, loadingId: string }) => {

    const tableBody = () => {
        if (data != undefined) {
            if (type === Type.Edit) {
                return (<TableBody showEdit={showEdit} data={data.Data} headers={data.EditMetadata.Sections} onEdit={() => { return null }}
                    onChange={onChange} onSelect={onSelect} isView={false}
                    handleSelectAll={handleSelectAll} loadingId={loadingId}
                />);
            }
            return (<TableBody showEdit={showEdit} data={data.Data} headers={data.ViewMetadata.Sections}
                onEdit={onEdit} onSelect={onSelect} onChange={onChange} isView={true}
                handleSelectAll={handleSelectAll} loadingId={loadingId}
            />);
        }
        return null;
    }

    return (
        <div className="box">
            <div className="header">{title}s</div>
            {data === undefined
                ? <Loading />
                : <div className="body">
                    <Table>
                        {tableBody()}
                    </Table>
                    {type === Type.View && showEdit && recordJson["Id"] != undefined
                        ? <Button className="btn" onClick={onAdd}>
                            <i className={fa.faPlusCircle}></i>{" "} Add {title}
                        </Button>
                        : null}
                </div>}
        </div>
    );
}

export { Type };
export default grid;


