import logger, { LogState } from "./Logger";

class DomParserHelper {
    parse(str: string | number): string {
        if (str === undefined || str === null) return "";

        const parser = new DOMParser();
        const dom = parser.parseFromString(String(str), "text/html");

        return dom.body.textContent as string;
    }

    decode(str: string | number | boolean): string | number | boolean {
        if (str === undefined || str === null) return "";

        if (typeof (str) == "boolean") return str;
        if (typeof (str) == "number") return str;

        str = str.replace(/\</g, "&lt;");
        str = str.replace(/\>/g, "&gt;");

        return str;
    }
}

const domParserHelper = new DomParserHelper();

export default domParserHelper;




