import * as React from 'react';
import EbtLoading from "./common/EbtLoading";
import domParserHelper from "../lib/DomParserHelper";

interface IMasterPageState {
    packet: any,
}

interface IMasterPageProps {
    packet: any,
    loaded: boolean;
}

class MasterPage extends React.Component<IMasterPageProps, IMasterPageState>
{
    constructor() {
        super();
        this.state = {
            packet: undefined
        }
        this.loadObject = this.loadObject.bind(this);
    }

    componentWillMount() {
        this.setState({ packet: this.props.packet });
    }

    componentWillReceiveProps(nextProps: IMasterPageProps) {
        this.setState({ packet: nextProps.packet });
    }

    loadObject() {
        if (this.state.packet["Entity"] === undefined)
            return null;
        return (<div>
            <div className="heading">
                <div>
                    <i className="heading-img" />
                </div>
                <div className="right">
                    <div className="type"><a href={`/${this.state.packet.Entity.Id}`} target="_blank">{this.state.packet.Entity.Name}</a></div>
                    <div className="description">{this.state.packet.PacketDetail.Name}</div>
                </div>
            </div>
            <div>
                {this.props.children}
            </div>
        </div>);
    }

    render() {
        if (this.state.packet != undefined) {
            return (
                <div>
                    {this.props.loaded ? this.loadObject() : <EbtLoading />}
                </div>
            );
        }
        return null;
    }
}

export default MasterPage;


