import IApi, { IStages, IStageLog, ITmServiceLinks, ITmoSigner, IStageNotesResponse, IStageNotesSaveResponse, ISigProducts, IApproval, IApprovalSubmitResponse, ILookupResult } from "./IApi";
declare var api: IApi;
import * as _ from "../lodash";
import { ValidationButtonState } from "../components/common/pageValidator";
import { ISignerInfo } from "../components/OutForSignature";
import logger, { LogState } from "../lib/Logger";

export interface ITMRepository {
    bootstrap(): Promise<IResult | null>;
    bootstrapEntity(): Promise<IResult | null>;
    loadModalInformation(jsonData: IModalJsonData, modalType: ModalType): Promise<IModalInfo | null>;
    loadNewModalInformation(jsonData: IModalJsonData, modalType: ModalType): Promise<IModalInfo | null>;
    saveModal(data: any, isNew: boolean, newModel: any, modalType: ModalType): Promise<IModalInfo | null>;
    saveRequest(data: any): Promise<ISaveResponse>;
    generatePdf(): Promise<string>;
    generatePacketPdf(packetId: string, includeMasterAgreement: boolean): Promise<string>;
    generateSummaryContactPdf(packetId: string): Promise<any>;
    generateUserSetupView(packetId: string): Promise<any>;
    generateNewAccountNumber(accountType: string): Promise<IGenerateNumberResult>;
    searchForAccount(searchPhrase: string): Promise<ISearchResult[]>;
    getTmOperationsScreen(): Promise<string>;
    getValidations(request: any): Promise<any>;
    archive(): Promise<any>;
    getTemplatesExist(productName: string): Promise<any>;
    getAllStages(): Promise<IStages[]>;
    validateRequest(stages: string[]): Promise<IValidationResult[]>;
    validateTmRequestsByCurrentStage(packetId: string, tmRequests: string[]): Promise<IValidationResult[]>;
    validateOutForSignatureRequests(stages: string[], product: ISigProducts[]): Promise<IValidationResult[]>;
    validateRequestsForSignature(signatureInfo: ISignerInfo, stageIds: string[]): Promise<IValidationResult[]>;
    updateStage(stageId: string): Promise<boolean>;
    getStageLogs(): Promise<IStageLog[]>;
    getApprovalInfo(): Promise<IApproval>;
    submitApprovalAction(action: string): Promise<IApprovalSubmitResponse>;
    updateAssignees(packetId: string, assigneeName: string): Promise<boolean>;
    sendForSignature(signerInfo: ISignerInfo, outForSignatureStageId: string): Promise<boolean>;
    getTmServiceArchiveLinks(): Promise<ITmServiceLinks[]>;
    getTmos(): Promise<ITmoSigner[]>;
    saveStageNotes(stageNotes: string): Promise<IStageNotesSaveResponse>;
    getStageNotes(): Promise<IStageNotesResponse>;
    getLookup(lookupField: string, searchItem: string): Promise<ILookupResult[]>;
    removeTmProductFromPacket(): Promise<boolean>;
}

export interface ISearchResult {
    Name: string,
    AccountNumber: string;
    Id: string;
    Address: string;
    Entity: string;
}

export interface ISaveResponse {
    ResponseStatus: SaveStatus;
    Errors: string[];
    ValidationState?: ValidationButtonState;
}

export enum SaveStatus {
    Saved,
    Failed
}

export interface IResult {
    Accounts: any;
    AccountNumberTypes: any;
    Contacts: any;
    Details: any;
    Entity: any;
    ErrorDetails: any;
    Errors: any;
    Packet: any;
    StageDetails: any;
    SelectedAccounts: any;
    SelectedContacts: any;
    PermissionDetails: any;
}

export interface IModalInfo {
    ModalTitle: string;
    ChildDetailsTitle: string;
    ParentDetails: any;
    ChildDetails: any;
    ModalType: ModalType;
    Errors: any[];
}


export enum ModalType {
    None,
    Account,
    Contact
}

export interface IModalJsonData {
    ParentId: string;
    RelatedIds: string[];
}

export interface IGenerateNumberResult {
    AccountNumber: string;
    Errors: string[];
}

export interface IValidationResult {
    Stage: string;
    StageId: string;
    StageType: string;
    Errors: string[];
    Product: string;
}


class TMRespository implements ITMRepository {
    saveStageNotes(stageNotes: string): Promise<IStageNotesSaveResponse> {
        return api.saveStageNotes(stageNotes);
    }

    getStageNotes(): Promise<IStageNotesResponse> {
        return api.getStageNotes();
    }

    getTmServiceArchiveLinks(): Promise<ITmServiceLinks[]> {
        return api.getTmServiceArchiveLinks();
    }

    getStageLogs(): Promise<IStageLog[]> {
        return api.getStageLogs();
    }

    getApprovalInfo(): Promise<IApproval> {
        return api.getApprovalInfo();
    }

    submitApprovalAction(action: string): Promise<IApprovalSubmitResponse> {
        return api.submitApprovalAction(action);
    }

    updateStage(stageId: string): Promise<boolean> {
        return api.updateStage(stageId);
    }

    sendForSignature(signerInfo: ISignerInfo, outForSignatureStageId: string): Promise<boolean> {
        logger.log(LogState.Debug, signerInfo);
        return api.sendForSignature(signerInfo, outForSignatureStageId);
    }
    getAllStages(): Promise<IStages[]> {
        return api.getAllStages().then(x => {
            return x;
        });
    }
    updateAssignees(packetId: string, assigneeName: string): Promise<boolean> {
        return api.updateAssignee(packetId, assigneeName);
    }

    validateRequest(stageIds: string[]): Promise<IValidationResult[]> {
        return api.validateRequest(stageIds).then(validationDetails => {
            let results: IValidationResult[] = [];
            if (validationDetails === undefined || validationDetails === null) { return results; }
            logger.log(LogState.Debug, "validation details", validationDetails, results);

            for (let sd of validationDetails.StageDetails) {
                const validations = validationDetails.ValidationsByStage[sd.Id];
                if (_.find(validations, { IsValid: false }) !== undefined) {
                    const vr: IValidationResult = {
                        Errors: [],
                        Stage: sd.Name,
                        StageId: sd.Id,
                        StageType: sd.StageType,
                        Product: ""
                    };
                    for (let v of validations) {
                        if (!v.IsValid) {
                            vr.Errors.push(v.Error);
                        }
                    }
                    results.push(vr);
                }
            }
            return results;
        });
    }

    validateTmRequestsByCurrentStage(packetId: string, tmRequests: string[]): Promise<IValidationResult[]> {
        return api.validateTmRequestsByCurrentStage(packetId, tmRequests).then(x => {
            let results: IValidationResult[] = [];

            logger.log(LogState.Debug, "validation of requests by current stage", x);

            const vr: IValidationResult = {
                Errors: x,
                Stage: "",
                StageId: "",
                StageType: "",
                Product: ""
            };

            results.push(vr);

            return results;
        });
    }

    //HACK: this is not the best way to do this, please redo it
    validateOutForSignatureRequests(stageIds: string[], products: ISigProducts[]): Promise<IValidationResult[]> {
        return api.validateOutForSignatureRequests(stageIds, products).then(validationDetails => {
            logger.log(LogState.Debug, "validation results", validationDetails);
            let results: IValidationResult[] = [];
            if (validationDetails === undefined || validationDetails === null) { return results; }
            logger.log(LogState.Debug, "validation out for details details", validationDetails, results);
            for (let validationDeets of validationDetails.filter(x=> x !== null)) {
                logger.log(LogState.Debug, "validation deets", validationDeets);
                for (let sd of validationDeets.StageDetails) {
                    const validations = validationDeets.ValidationsByStage[sd.Id];
                    if (_.find(validations, { IsValid: false }) !== undefined) {
                        const vr: IValidationResult = {
                            Errors: [],
                            Stage: sd.Name,
                            StageId: sd.Id,
                            StageType: sd.StageType,
                            Product: validationDeets.ProductId || ""
                        };
                        for (let v of validations) {
                            if (!v.IsValid) {
                                vr.Errors.push(v.Error);
                            }
                        }
                        results.push(vr);
                    }
                }
            }

            return results;
        });
    }

    async validateRequestsForSignature(signatureInfo: ISignerInfo, stageIds: string[]): Promise<IValidationResult[]> {
        const errors = await api.validateTmRequests(signatureInfo.packetId, signatureInfo.tmRequests, stageIds);
        return errors.map((x: any) => {
            return {
                Errors: [x.Error],
                Product: x.Product
            } as IValidationResult;
        });
    }

    getTemplatesExist(productName: string): Promise<any> {
        return api.getTemplatesExist(productName).then(x => {
            return x;
        });
    }

    archive(): Promise<any> {
        return api.archive().then(x => {
            return x;
        });
    }

    generateSummaryContactPdf(packetId: string): Promise<any> {
        return api.generateSummaryContactPdf(packetId).then(x => {
            return x;
        });
    }

    generateUserSetupView(packetId: string): Promise<any> {
        return api.generateUserSetupView(packetId);
    }

    getValidations(request): Promise<any> {
        return api.getValidations(request);
    }

    getTmOperationsScreen() {
        return api.getTmOperationsScreen();
    }
    searchForAccount(searchPhrase: string): Promise<ISearchResult[]> {
        return api.searchForAccount(searchPhrase).then((x: ISearchResult[]) => {
            return x;
        });
    }

    generateNewAccountNumber(accountType: string): Promise<IGenerateNumberResult> {
        return api.generateNewAccountNumber(accountType).then((x: IGenerateNumberResult) => {
            return x;
        });
    }

    generatePacketPdf(packetId: string, includeMasterAgreement: boolean): Promise<string> {
        return api.generatePacket(packetId, includeMasterAgreement).then(x => {
            return x;
        });
    }

    generatePdf(): Promise<string> {
        return api.generatePdf().then(x => {
            return x;
        });
    }

    saveRequest(data): Promise<ISaveResponse> {
        var response: ISaveResponse = Object.create({});
        return api.saveRequest(data).then(x => {
            if (x.length > 0) {
                response.ResponseStatus = SaveStatus.Failed;
                response.Errors = x;
            } else {
                response.ResponseStatus = SaveStatus.Saved;
            }
            return response;
        });
    }

    async saveModal(data, isNew: boolean, newModel, modalType: ModalType): Promise<IModalInfo | null> {
        var result: IModalInfo = Object({ Errors: [] });

        if (modalType === ModalType.Account) {
            const accountErrors: any[] = await api.saveAccounts(data, isNew, newModel);
            logger.log(LogState.Debug, "account errors", accountErrors);
            if (accountErrors.length !== 0) {
                result.Errors = accountErrors;
                return result;
            }
        } else if (modalType === ModalType.Contact) {
            const contactErrors: any[] = await api.saveContacts(data, isNew);
            if (contactErrors.length !== 0) {
                result.Errors = contactErrors;
                return result;
            }
        }

        result.ModalType = ModalType.None;
        return result;
    }

    loadNewModalInformation(jsonData: IModalJsonData, modalType: ModalType): Promise<IModalInfo | null> {
        var result: IModalInfo = Object.create({});

        if (modalType === ModalType.Account) {
            return api.loadAccountModalInformation(jsonData).then(x => {
                x.Parent["Data"] = [{}];
                result.ModalTitle = "Add New Account";
                result.ChildDetailsTitle = "Account Contact";
                result.ParentDetails = x.Parent;
                result.ChildDetails = x.Children;
                result.ModalType = ModalType.Account;
                return result;
            });
        } else if (modalType === ModalType.Contact) {
            return api.loadContactModalInformation(jsonData).then(x => {
                x.Parent["Data"] = [{}];
                result.ModalTitle = "Add New Contact";
                result.ChildDetailsTitle = "Contact Account";
                result.ParentDetails = x.Parent;
                result.ChildDetails = x.Children;
                result.ModalType = ModalType.Contact;
                return result;
            });
        } else {
            return new Promise((resolve, reject) => {
                resolve(null);
                reject(null);
            });
        }
    }

    loadModalInformation(jsonData: IModalJsonData, modalType: ModalType): Promise<IModalInfo | null> {
        return new Promise((resolve, reject) => {
            var result: IModalInfo = Object.create({});

            if (modalType === ModalType.Account) {
                return api.loadAccountModalInformation(jsonData).then(x => {
                    result.ChildDetails = x.Children;
                    result.ModalTitle = x.Parent.Data[0]["ParentName"];
                    result.ChildDetailsTitle = "Contact Account";
                    result.ModalType = ModalType.Account;
                    result.ParentDetails = x.Parent;
                    result.ChildDetails.Data.forEach(x => {
                        x["ParentId"] = jsonData.ParentId;
                    });
                    resolve(result);
                });
            } else if (modalType === ModalType.Contact) {
                return api.loadContactModalInformation(jsonData).then(x => {
                    result.ChildDetails = x.Children;
                    result.ModalTitle = x.Parent.Data[0]["ParentName"];
                    result.ChildDetailsTitle = "Account Contact";
                    result.ModalType = ModalType.Contact;
                    result.ParentDetails = x.Parent;
                    result.ChildDetails.Data.forEach(x => {
                        x["ParentId"] = jsonData.ParentId;
                    });
                    resolve(result);
                });
            } else {
                console.error("there was an error with loading modal information");
                resolve(null);
                reject(null);
            }
        });
    }

    public bootstrap(): Promise<IResult | null> {
        var result: IResult = Object.create({});

        return api.bootstrap().then((x: any) => {
            const selectedAccounts: any[] = [];
            const selectedContacts: any[] = [];
            x.Accounts.Data.filter(x => Object.getOwnPropertyNames(x).length !== 0).forEach(acc => {
                if (acc.Included.Selected) {
                    selectedAccounts.push(acc.Included.Id);
                }
            });
            x.Contacts.Data.filter(x => Object.getOwnPropertyNames(x).length !== 0).forEach(acc => {
                if (acc.Included.Selected) {
                    selectedContacts.push(acc.Included.Id);
                }
            });
            result.Accounts = x.Accounts;
            result.Contacts = x.Contacts;
            result.Details = x.Details;
            result.SelectedAccounts = selectedAccounts;
            result.SelectedContacts = selectedContacts;
            result.Packet = x.Packet;
            result.StageDetails = x.StageDetails;
            result.PermissionDetails = x.PermissionDetails;
            result.AccountNumberTypes = x.AccountNumberTypes;
            result.Entity = x.Entity;

            logger.log(LogState.Debug, "bootstrap results", result);
            return result;

        });
    }

    public bootstrapEntity(): Promise<IResult | null> {
        var result: IResult = Object.create({});

        return api.bootstrapEntity().then((x: any) => {
            result.Packet = x.Packet;
            result.StageDetails = x.StageDetails;
            result.PermissionDetails = x.PermissionDetails;
            result.AccountNumberTypes = x.AccountNumberTypes;
            result.Entity = x.Entity;

            logger.log(LogState.Debug, "bootstrap entity results", result);

            return result;
        });
    }

    async getTmos(): Promise<ITmoSigner[]> {
        const tmos = await api.getTmos();
        const tmosArray: ITmoSigner[] = [];
        tmos.forEach(x => {
            const tmoSplit = x.split('|');
            tmosArray.push({
                FirstName: tmoSplit[0],
                LastName: tmoSplit[1],
                Email: tmoSplit[2],
                Phone: tmoSplit[3]
            });
        });

        return tmosArray;
    }

    public getLookup(lookupField: string, searchItem: string): Promise<ILookupResult[]> {
        return api.getLookup(lookupField, searchItem);
    }

    public async removeTmProductFromPacket():Promise<boolean> {
        return api.removeTmProductFromPacket();
    }
}

export default TMRespository;


