import * as React from 'react';
import HelpText from './HelpText';

const LinkField = ({ label, helpText, href, text, openInNewTab }: { label: string, helpText: string, href: string, text: string, openInNewTab?: boolean }) =>
{
    label = label === undefined ? "" : label;
    openInNewTab = openInNewTab === undefined ? false : openInNewTab;

    return (
        <span className="inner-row">
            <div className="label">
                <label>
                    {label}
                </label>
            </div>
            <HelpText helpText={helpText} />
            <div className="value linkValue">
                <a href={href} title={href} target={openInNewTab ? "_blank" : "_self"}>{text}</a>
            </div>
        </span>
    );
}

export default LinkField;


