import * as React from "react";
import * as fa from "./common/FontAwesome";
import Modal from "./common/Modal";
import Button from "./common/Button";
import Dropdown, { IOption } from './common/Dropdown';
import Errors from "./common/Errors";
import TMRepository, { ITMRepository, ISearchResult, IGenerateNumberResult } from "../api/TMRepository";
import { ValidationEnum } from "./common/InputField";
import htmlParser from "../lib/DomParserHelper";
import { setStateAsync } from "../lib/Helpers";


interface INewAccountModal
{
    onSave(e: any): Promise<boolean>;
    onCancel: any;
    isOpen: boolean;
    errors: string[];
    accountTypes: IOption[];
}

enum ButtonState
{
    New,
    Filled
}

interface INewAccountModalState
{
    saving?: boolean;
    errors: string[];
    buttonState: ButtonState;
    objects: IObjects;
    showResults: boolean;
    results?: ISearchResult[];
}

interface IObjects
{
    search: string;
    accountName: string;
    existingAccountId: string;
    toa: string;
}

class NewAccountModal extends React.Component<INewAccountModal, INewAccountModalState> {
    private readonly _tmRepository: ITMRepository;
    constructor()
    {
        super();
        this._tmRepository = new TMRepository();

        this.state = {
            saving: false,
            errors: [],
            objects: {
                existingAccountId: "",
                search: "",
                accountName: "",
                toa: ""
            },
            buttonState: ButtonState.New,
            showResults: false,
        }
        this.onSaving = this.onSaving.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onAddNewAccount = this.onAddNewAccount.bind(this);
        this.onSearchAccount = this.onSearchAccount.bind(this);
        this.onSelectSearchedAccount = this.onSelectSearchedAccount.bind(this);
        this.onClearAccount = this.onClearAccount.bind(this);
        this.resultsTable = this.resultsTable.bind(this);
        this.renderBody = this.renderBody.bind(this);
        this.onCancel = this.onCancel.bind(this);
    }


    async onSaving()
    {
        if (this.state.objects.search.length === 0 || this.state.objects.accountName.length === 0)
        {
            await setStateAsync<INewAccountModalState>(this, { errors: ["Please fix errors before saving."] });
            return;
        }
        var newModel = {
            existingAccountId: this.state.objects.existingAccountId,
            accountNumber: this.state.objects.search,
            accountName: this.state.objects.accountName
        }
        await setStateAsync<INewAccountModalState>(this, { saving: true });
        var didSave = await this.props.onSave(newModel);
        if (didSave)
        {
            const { objects } = this.state;
            objects["SelectAccountType"] = "";
            await setStateAsync<INewAccountModalState>(this, { errors: [], saving: false, objects });
        }
    }

    componentWillReceiveProps(nextProps: INewAccountModal)
    {
        if (nextProps.errors.length > 0)
        {
            this.setState({ errors: nextProps.errors, saving: false });
        }
    }

    onAddNewAccount()
    {
        if (this.state.objects.toa.length === 0)
        {
            this.setState({ errors: ['Please choose an account type.'] });
            return null;
        }
        this.setState({ errors: [] });

        this._tmRepository.generateNewAccountNumber(this.state.objects.toa).then((x: IGenerateNumberResult) =>
        {

            var objects = this.state.objects;
            objects.search = x.AccountNumber;
            this.setState({ objects, buttonState: ButtonState.Filled });
        });
    }

    onSearchAccount()
    {
        this._tmRepository.searchForAccount(this.state.objects.search).then(x =>
        {
            this.setState({ showResults: true, results: x });
        });

    }

    onSelectSearchedAccount(e: ISearchResult)
    {
        var objects = this.state.objects;
        objects.search = e.AccountNumber;
        objects.accountName = e.Name;
        objects.existingAccountId = e.Id;
        this.setState({ objects, buttonState: ButtonState.Filled, showResults: false });
    }

    onClearAccount()
    {
        var objects = this.state.objects;
        objects.search = "";
        objects.existingAccountId = "";
        objects.accountName = "";
        this.setState({ objects, buttonState: ButtonState.New, showResults: false, errors: [] });
    }

    onChange(e)
    {
        var t = e.target;
        var objects = this.state.objects;
        objects[t.name] = t.value;
        if (t.name === "SelectAccountType")
            this.onClearAccount();
        this.setState({ objects });
    }

    renderBody()
    {
        if (this.state.objects["SelectAccountType"] === "New")
        {
            return (<div className="box">
                <div className="header">New Account</div>
                <div className="body">
                    <div className="flex-row columns_1">
                        <div className="col">
                            <Dropdown label="Type of Account" name="toa" onChange={this.onChange} options={this.props.accountTypes} selected={this.state.objects.toa} required={ValidationEnum.NotRequired} disabled={this.state.buttonState === ButtonState.Filled} />

                        </div>
                        <div className="col">
                            <div className="inner-row">
                                <div className="label">
                                    Account
                            </div>
                                <i className="fa fa-question-circle helptext hidden"></i>
                                <div className="value">
                                    {this.state.objects.search.length !== 0 ? <input type="text" disabled={true} name="search" value={this.state.objects.search} /> : null}
                                    {this.state.buttonState !== ButtonState.Filled ?
                                        <Button className="btn" onClick={this.onAddNewAccount}>Generate</Button>
                                        :
                                        <Button className="btn" onClick={this.onClearAccount}>Clear</Button>
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="col">
                            <div className="inner-row">
                                <div className="label">
                                    Account Name
                                </div>
                                <i className="fa fa-question-circle helptext hidden"></i>
                                <div className="value">
                                    <input type="text" name="accountName" value={
                                        this.state.objects.accountName} onChange={this.onChange} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>);
        } else if (this.state.objects["SelectAccountType"] === "Existing")
        {
            return (<div className="box">
                <div className="header">Existing Account</div>
                <div className="body">
                    <div className="flex-row columns_1">
                        <div className="col">
                            <div className="inner-row">
                                <div className="label">
                                    Account
                                </div>
                                <i className="fa fa-question-circle helptext hidden"></i>
                                <div className="value">
                                    <input type="text" disabled={this.state.buttonState === ButtonState.Filled
                                    } name="search" value={this.state.objects.search} onChange={this.onChange} />
                                    {this.state.buttonState !== ButtonState.Filled ?
                                        <Button className="btn" onClick={this.onSearchAccount}>Search</Button>
                                        :
                                        <Button className="btn" onClick={this.onClearAccount}>Clear</Button>
                                    }
                                </div>
                            </div>
                        </div>
                        {this.resultsTable()}
                        <div className="col">
                            <div className="inner-row">
                                <div className="label">
                                    Account Name
                                </div>
                                <i className="fa fa-question-circle helptext hidden"></i>
                                <div className="value">
                                    <input type="text" name="accountName" disabled={this.state.objects["SelectAccountType"] === "Existing"} value={
                                        this.state.objects.accountName} onChange={this.onChange} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>);
        }
        return null;
    }

    resultsTable()
    {
        if (this.state.results == undefined || !this.state.showResults)
            return null;
        return (
            <div className="col maxHeight">
                <table className="table">
                    <thead>
                        <tr className="headerRow">
                            <th></th>
                            <th>Account Number</th>
                            <th>Account Name</th>
                            <th>Address</th>
                            <th>Entity</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.results.map((x: ISearchResult, i: number) =>
                        {
                            return (<tr key={i}>
                                <td className="align-center"><a onClick={() =>
                                {
                                    this.onSelectSearchedAccount(x);
                                }}>Select</a></td>
                                <td>{x.AccountNumber}</td>
                                <td>{x.Name}</td>
                                <td>{x.Address}</td>
                                <td>{x.Entity}</td>
                            </tr>);
                        })}
                    </tbody>
                </table>
            </div>);
    }

    onCancel()
    {
        this.onClearAccount();
        this.props.onCancel();
    }

    dropdownOptions(): IOption[]
    {
        return [
            {
                Value: "",
                Label: "Select Account Type",
                Enabled: false
            },
            {
                Value: "New",
                Label: "New Account",
                Enabled: true
            },
            {
                Value: "Existing",
                Label: "Existing Account",
                Enabled: true
            }
        ];
    }


    render()
    {
        return (
            <Modal show={this.props.isOpen}>
                <Modal.Header>
                    <div className="type">TM Request</div>
                    <div className="description">Add a new Account</div>
                </Modal.Header>
                <Modal.Body>
                    <Errors errors={this.state.errors} />

                    <div className="box">
                        <div className="body">
                            <div className="flex-row columns_1">
                                <div className="col">
                                    <Dropdown name="SelectAccountType" label="Select Account Type" onChange={this.onChange} options={this.dropdownOptions()} selected={this.state.objects["SelectAccountType"]} required={ValidationEnum.NotRequired} />
                                </div>
                            </div>
                        </div>
                    </div>
                    {this.renderBody()}
                </Modal.Body>
                <Modal.Footer>
                    <Button className="btn" onClick={this.onSaving} disabled={this.state.saving}>
                        {this.state.saving ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : null}
                        {" "}
                        Save Account</Button>
                    <Button className="btn" onClick={this.onCancel} disabled={this.state.saving}>Close</Button></Modal.Footer>
            </Modal>
        );
    }
}

export default NewAccountModal;


