import * as React from 'react';
import Checkbox from "../common/CheckBox"
import Dropdown from '../common/Dropdown';
import Lookup from "../common/Lookup";
import Textarea from '../common/Textarea';
import Textbox from '../common/Textbox';
import Datepicker from '../common/Datepicker';
import RadioButton from '../common/RadioButton';
import * as numeral from 'numeral';
import MultiPicklist, { IValues } from '../common/MultiPicklist';
import MultiPills from '../common/MultiPills';
import HelpText from './HelpText';
import * as moment from "moment";
import Link from './Link';
import LinkField from './LinkField';
import domParser from "../../lib/DomParserHelper";

interface IInputFieldProps {
    inputType: any,
    isView: boolean,
    onChange: Function,
    index: number,
    name: string,
    id: string,
    label: string,
    data: string,
    options: any,
    helpText?: string | undefined;
    validation?: any;
    readOnly?: boolean;
}

export enum ValidationEnum {
    NotRequired,
    ImmediateRequired,
    FutureRequired,
    Correlated
}

class InputField extends React.Component<IInputFieldProps, any> {

    constructor(props: IInputFieldProps) {
        super(props);
        this.onChange = this.onChange.bind(this);
    }

    convertDate(dateObj: Date) {
        return moment(dateObj).utc().format("L");
    }

    addZero(number: number, length: number) {
        let result = "";
        const numberLen = number.toString().length;

        if (numberLen < length) {
            for (let i = 0; i < length - numberLen; i++) {
                result += "0";
            }
        }

        result += number;
        return result;
    }

    toCurrency(currencyObj: string) {
        const number = numeral(currencyObj);
        const is3DecimalPlaces = /\.\d{3,}/g.test(number._value);
        if (is3DecimalPlaces) {
            numeral.defaultFormat('$0,0.000');
        } else
            numeral.defaultFormat('$0,0.00');
        return number.format();
    }

    renderView(inputType: any, data: any, options: any) {
        if (inputType !== 'Blank' && (data === undefined || data.length === 0))
            return <i className="empty_field"></i>;
        if (/date/gi.test(inputType))
            return this.convertDate(new Date(data));
        if (/currency/gi.test(inputType))
            return `${this.toCurrency(data)}`;
        if (/phone/gi.test(inputType)) {
            let phone = data.replace(/[^\d]/gi, "");
            phone = phone.replace(/(\d{3})(\d{3})(\d{4})/gi, /($1) $2-$3/);
            return phone;
        }
        if (/tag/gi.test(inputType)) {
            return (<div className="multi-pills">
                {data.split(";").filter(x => x.length > 0).map((x: string, i: number) => {
                    return <div className="pill" key={i}>{x}</div>;
                })}
            </div>);
        }

        return data;
        //  return domParser.parse(data);
        //  return <div className="pill" key={i}>{domParser.parse(x)}</div>;
    }

    onChange(x: any) {
        const readOnly = this.props.readOnly === undefined ? false : this.props.readOnly;

        if (!readOnly)
            this.props.onChange(x);
    }

    render() {
        let validEnum = ValidationEnum.NotRequired;
        if (this.props.validation) {
            if (this.props.validation.Correlated || this.props.validation.ParentId !== "") {
                validEnum = ValidationEnum.Correlated;
            }
            else if (this.props.validation.Immediate) {
                validEnum = ValidationEnum.ImmediateRequired;
            } else {
                validEnum = ValidationEnum.FutureRequired;
            }
        }
        if (/checkbox/gi.test(this.props.inputType))
            return <Checkbox readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} disabled={this.props.isView}
                name={this.props.name} label={this.props.label} checked={this.props.data} helpText={this.props.helpText}
                required={validEnum} />;
        else if (/radio/gi.test(this.props.inputType))
            return <RadioButton readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} disabled={this.props.isView}
                name={this.props.name} label={this.props.label} checked={this.props.data} helpText={this.props.helpText}
                id={this.props.id} required={validEnum} />;
        else if (/dropdown/gi.test(this.props.inputType) && !this.props.isView)
            return <Dropdown readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} name={this.props.name}
                label={this.props.label} options={this.props.options} selected={this.props.data} helpText={this.props
                    .helpText} required={validEnum} />;
        /*else if (/lookup/gi.test(this.props.inputType))
            return <Lookup key={this.props.index} onOpenLookup={this.onChange} name={this.props.name}
                label={this.props.label} selected={this.props.data} helpText={this.props
                    .helpText} required={validEnum} isView={this.props.isView} />;*/
        else if (/textarea/gi.test(this.props.inputType) && !this.props.isView)
            return (<Textarea readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} name={this.props.name}
                label={this.props.label} text={this.props.data} helpText={this.props.helpText} required={validEnum} />);
        else if (/text/gi.test(this.props.inputType) && /https?:\/\//gi.test(this.props.data))
            return <LinkField href={this.props.data} label={this.props.label} helpText={this.props.helpText}
                openInNewTab={true} text={this.props.data} />
        else if (/date/gi.test(this.props.inputType) && !this.props.isView)
            return (<Datepicker readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} name={this.props.name}
                label={this.props.label} data={this.props.data} />);
        else if (/text|numeric|currency|phone/gi.test(this.props.inputType) && !this.props.isView)
            return (<Textbox readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} name={this.props.name}
                label={this.props.label} text={this.props.data} isNumeric={/numeric|currency/gi.test(this.props.inputType)}
                helpText={this.props.helpText} required={validEnum}  />
            );
        else if (/tag/gi.test(this.props.inputType) && !this.props.isView) {
            return (<MultiPills readOnly={this.props.readOnly} key={this.props.index} onChange={this.onChange} name={this.props.name} label={
                this.props.label} pills={this.props.data} helpText={this.props.helpText} required={validEnum} />);
        }
        else if (/multipicklist/gi.test(this.props.inputType)) {
            const multilist: IValues[] = [];
            this.props.options.forEach((x: any) => {
                const l: IValues = {
                    value: x.Value,
                    label: x.Label,
                    selected: false
                }
                multilist.push(l);
            });
            return (<MultiPicklist readOnly={this.props.readOnly} data={multilist} isView={this.props.isView} label={this.props.label} helpText={this
                .props.helpText} required={validEnum} />);
        } else if (this.props.isView) {
            const readOnlyClass = this.props.readOnly !== undefined && this.props.readOnly ? "readonly" : "";
            return (<div className="inner-row">
                <div className={"label " + readOnlyClass}>
                    <label>
                        {this.props.label}
                    </label>
                </div>
                <HelpText helpText={this.props.helpText} />
                <div className="value">
                    {this.renderView(this.props.inputType, this.props.data, this.props.options)}
                </div>
            </div>);
        }

        return <div key={this.props.index} className="empty"></div>;
    }


}

export default InputField;


