import * as React from 'react';
import Loading from "./common/Loading";
import Section from './details/section';
import { Type } from "./Grid";

interface IDetailsProps {
    data: any;
    onClick: any;
    type: Type;
    title?: string;
}

class Details extends React.Component<IDetailsProps, any> {
    constructor() {
        super();
        this.state = {
            sectionOpen: {},
            tabOpen: "",
            details: undefined
        }
        this.renderFields = this.renderFields.bind(this);
        this.onToggleSection = this.onToggleSection.bind(this);
        this.hasDataFields = this.hasDataFields.bind(this);
    }

    componentDidMount(): void {
        this.setState({ details: this.props.data });
    }

    componentWillReceiveProps(nextProps): void {
        this.setState({ details: nextProps.data });
    }

    onToggleSection(i: number) {
        let metadataType = "ViewMetadata";
        if (this.props.type === Type.Edit)
            metadataType = "EditMetadata";
        const details = this.state.details;
        if (details[metadataType].Sections[i]["Collapsed"] === undefined)
            details[metadataType].Sections[i]["Collapsed"] = false;

        details[metadataType].Sections[i]["Collapsed"] = !details[metadataType].Sections[i]["Collapsed"];
        this.setState({ details });
    }

    renderFields() {
        let metadataType = "ViewMetadata";
        if (this.props.type === Type.Edit)
            metadataType = "EditMetadata";
        if (this.state.details !== undefined) {
            return this.state.details[metadataType].Sections.map((section, i) => {                
                var hiddenFieldsCount = section.Fields.filter(x=> x.InputType !== "Blank").length;
                if (section.DependentFields != undefined) {
                    for (let j = 0; j < section.DependentFields.filter(x => x.InputType !== "Blank").length; j++) {
                        hiddenFieldsCount += section.DependentFields[j].Fields.filter(x => x.InputType !== "Blank").length;
                    }
                }
                return <Section show={!section["Collapsed"]} hiddenItems={hiddenFieldsCount} onChange={this.props.onClick} key={i} name={section.Name} data={this.state.details.Data[
                    0]} hideSection={() => { this.onToggleSection(i) }}
                    sectionMetadata={section.Fields} serviceOptions={section.DependentFields} columns={section
                        .Columns}
                    showTitle={section.Visible} type={this.props.type} />;
            });
        }
        return null;
    }

    getServiceOptions(section) {
        let serviceOptions = this.state.details.Metadata["ServiceOptions"];
        let result: any[] = [];
        if (serviceOptions != undefined) {
            serviceOptions.forEach(x => {
                if (this.state.details.Metadata.EditFields[section].filter(y => y.Name === x.Name)[0] != undefined) {
                    x["Section"] = section;
                    x["Show"] = this.state.details.Data[x.Name];
                }
                if (x.Section === section)
                    result.push(x);
            });
        }
        return result;
    }

    hasDataFields() {
        if (this.state.details === undefined)
            return false;
        let metadataType = "ViewMetadata";
        if (this.props.type === Type.Edit)
            metadataType = "EditMetadata";
        return this.state.details[metadataType].Sections.length > 0;
    }

    render() {
        if (!this.props.data) {
            return <Loading />;
        }
        return (
            <div>
                {this.hasDataFields() ?
                    <div className="box">
                        <div className="header">
                            <div>{this.props.title} Details</div>
                        </div>
                        <div className="body">
                            {this.renderFields()}
                        </div>
                    </div> : 
                    <div className="text-center spacing_allowed"><h3>Select and define necessary contacts and accounts for {this.props.title}.</h3></div>
                }
            </div>
        );
    }
}

export default Details;


