import DataBuilder, { IDataBuilder } from "./DataBuilder";
import { ITMRepository, ISaveResponse, SaveStatus } from "../api/TMRepository";
import { IMiscFunctions, IMiscFullValidation } from "./MiscFunctions";
import { ValidationButtonState } from "../components/common/pageValidator";
import { ISignerInfo } from "../components/OutForSignature";
import logger, { LogState } from "./Logger";

declare var recordJson: any;

export interface ISaveHelper {
    saveAndMoveStage(data: any, newStage: string, stageNotes: string): Promise<ISaveResponse>;
    validateRequestsForSignature(signerInfo: ISignerInfo, stageIds: string[]): Promise<ISaveResponse>;
    saveAndSendForSignature(signerInfo: ISignerInfo, outForSignatureStageId: string, stageNotes: string): Promise<boolean>;
    quickSave(data: any): Promise<ISaveResponse>;
    adhocValidate(data: any): Promise<IValidation>;
}

export interface IValidation {
    validatonPassed: boolean;
    errors: string[];
}

class SaveHelper implements ISaveHelper {
    private readonly _dataBuilder: IDataBuilder;
    private readonly _tmRepository: ITMRepository;
    private readonly _miscFunction: IMiscFunctions;

    constructor(dataBuilder: IDataBuilder, tmRepository: ITMRepository, miscFunction: IMiscFunctions) {
        this._dataBuilder = dataBuilder;
        this._tmRepository = tmRepository;
        this._miscFunction = miscFunction;
    }

    async saveAndSendForSignature(signerInfo: ISignerInfo, outForSignatureStageId: string, stageNotes: string): Promise<boolean> {
        logger.log(LogState.Debug, "save and send for signature", stageNotes);
        await this._tmRepository.saveStageNotes(stageNotes);
        return this._tmRepository.sendForSignature(signerInfo, outForSignatureStageId);
    }

    async validateRequestsForSignature(signerInfo: ISignerInfo, stageIds: string[]): Promise<ISaveResponse> {
        const errorResponse = await this._tmRepository.validateRequestsForSignature(signerInfo, stageIds);

        if (errorResponse.length > 0) {
            const errors: string[] = [];
            for (let e of errorResponse) {
                const errorMessage = `${e.Product}: ${e.Errors.join(', ')}`;
                errors.push(errorMessage);
            }
            return { Errors: errors, ValidationState: ValidationButtonState.Invalid } as ISaveResponse;
        }

        return { ValidationState: ValidationButtonState.Valid } as ISaveResponse;
    }

    saveAndMoveStage(data: any, newStage: string, stageNotes: string): Promise<ISaveResponse> {
        return new Promise((resolve) => {
            const detailsObject = this._dataBuilder.buildPageData(data, "Edit");
            this._tmRepository.saveRequest(detailsObject).then((x: ISaveResponse) => {
                if (x.ResponseStatus === SaveStatus.Saved) {
                    logger.log(LogState.Debug, "save and move stage", stageNotes);
                    this._tmRepository.saveStageNotes(stageNotes);
                    this._miscFunction.RunFullValidation(data.stageDetails.Id).then((y: IMiscFullValidation) => {
                        if ((data.stageDetails.StageType === 'AllValidationRequired' || data.stageDetails.StageType === 'End' || data.stageDetails.StageType == 'Archive') &&
                            y.State !== ValidationButtonState.Valid) {
                            resolve({ ResponseStatus: SaveStatus.Failed, Errors: y.Validations[0].Errors, ValidationState: y.State } as ISaveResponse);
                        } else if (y.State !== ValidationButtonState.Invalid) {
                            this._tmRepository.updateStage(newStage).then(() => {
                                resolve(x);
                            });
                        }
                        else {
                            resolve({ ResponseStatus: SaveStatus.Failed, Errors: y.Validations[0].Errors, ValidationState: y.State } as ISaveResponse);
                        }
                    });
                } else {
                    /*resolve(x);*/
                    resolve({ ResponseStatus: SaveStatus.Failed, Errors: x.Errors } as ISaveResponse);
                }
            });
        });
    }

    quickSave(data: any): Promise<ISaveResponse> {
        return new Promise((resolve) => {
            const detailsObject = this._dataBuilder.buildPageData(data, "Edit");
            this._tmRepository.saveRequest(detailsObject).then((x: ISaveResponse) => {
                resolve(x);
            });
        });
    }

    adhocValidate(data: any): Promise<IValidation> {
        return new Promise((resolve, reject) => {
            let errors: string[] = [];
            let detailsObject = this._dataBuilder.buildPageData(data, "Edit");
            detailsObject = detailsObject.filter(x => x.FieldName !== "Selected" && (x.ValidateNow || x.Correlated) && x.FieldName.indexOf(".null") < 0);
            if (detailsObject !== null && detailsObject !== undefined && detailsObject.length > 0) {
                const objectName = detailsObject[0].FieldName.match(/^\w+\.?/g);
                const validationFields: any[] = [];
                for (let dO of detailsObject) {
                    if (objectName !== null) {
                        validationFields.push({
                            FieldName: dO.FieldName,
                            FieldValue: dO.FieldValue
                        });
                    }
                }
                const validationRequest = {
                    ObjectName: objectName !== null ? objectName[0].replace(".", "") : "",
                    StageId: data.stageDetails.Id,
                    Product: data.packet.Type,
                    RequestId: data.packet.Request.Id,
                    Fields: validationFields
                };
                logger.log(LogState.Debug, "adhoc validation", validationRequest);
                this._tmRepository.getValidations(validationRequest).then(x => {
                    logger.log(LogState.Debug, "get validations", x);
                    if (x !== undefined && x !== null) {
                        const failures = x.filter(x => !x.IsValid);
                        for (let f of failures) {
                            errors.push(f.Error);
                        }
                    }
                });
            } else {
            }

            resolve({
                validatonPassed: errors.length === 0,
                errors: errors
            } as IValidation);
        });
    }
}


export default SaveHelper;


