import * as React from 'react';
import Tabs from "./Tabs";
import * as fa from "../common/FontAwesome";
import { Type } from "../Grid";
import InputField from "../common/InputField";
import MultilistSection from '../common/MultiPicklistForSection';
import * as _ from "../../lodash";
import domParserHelper from '../../lib/DomParserHelper';

interface ISectionProps
{
    name: string;
    showTitle: boolean;
    data: any;
    sectionMetadata: any;
    serviceOptions?: any;
    onChange: any;
    columns: number;
    type: Type;
    hideSection: any;
    hiddenItems?: number;
    show: boolean;
}

interface IObject
{
    HelpText: string;
    InputType: string;
    Label: string;
    Multilist: string;
    Name: string;
    ObjectId: string;
    Options: any[];
    AlternateName: string;
    SortOrder: number;
    Validation: any;
    Readonly: boolean;
}

enum Columns
{
    One = 1,
    Two = 2,
    Three = 3,
    Four = 4,
    Six = 6,
    Twelve = 12
}

class Section extends React.Component<ISectionProps, any> {
    constructor()
    {
        super();
        this.state = {
            open: true,
            data: undefined,
            serviceOptions: undefined,
            sectionMetadata: undefined
        }
        this.toggleSection = this.toggleSection.bind(this);
        this.recievedProps = this.recievedProps.bind(this);
        this.renderField = this.renderField.bind(this);
        this.renderRows = this.renderRows.bind(this);
        this.onChange = this.onChange.bind(this);
        this.renderServiceOptions = this.renderServiceOptions.bind(this);
        this.onFocusOut = this.onFocusOut.bind(this);
    }

    componentWillReceiveProps(nextProps): void
    {
        this.recievedProps(nextProps);
    }

    componentDidMount(): void
    {
        this.recievedProps(this.props);
    }

    recievedProps(props: ISectionProps)
    {
        this.setState({ open: props.show, columns: props.columns, data: props.data, serviceOptions: props.serviceOptions, sectionMetadata: props.sectionMetadata });
    }

    toggleSection()
    {
        const o = this.state.open;
        this.setState({ open: !o }, () =>
        {
            this.props.hideSection(!o);
        });
    }

    onFocusOut(e)
    {
        //TODO: parse the fields somewhere
        const value = e.target.value;
        const name = e.target.name;
        if (e.target.type !== 'checkbox' && e.target.type !== 'radio')
        {
            const data = this.state.data;
            //data[name] = domParserHelper.decode(value);
            this.setState({ data });
        }
    }

    onChange(e)
    {
        const data = this.state.data;
        const serviceOptions = this.state.serviceOptions;
        const name = e.target.name;
        const value = e.target.value;
        const checked = e.target.checked;

        /*if (e.target.getAttribute('type') == "lookup") {
            const lookupName = e.target.getAttribute('name');
            //data[lookupName] = "some-lookup";
        }*/
        if (e.target.type === "checkbox")
            data[name] = checked;
        else if (e.target.type === "radio")
        {
            var allRadioButtons = document.querySelectorAll(`[type='radio'][name='${name}'`);
            var id = e.target.id;
            data[id] = checked;
            for (let i = 0; i < allRadioButtons.length; i++)
            {
                if (allRadioButtons[i].id !== id)
                {
                    var isChecked = allRadioButtons[i]["checked"];
                    data[allRadioButtons[i].id] = false;
                }
            }
        }
        else
            data[name] = value;

        if (serviceOptions != undefined && checked != undefined)
        {
            const so = serviceOptions.filter(x => x.Name === name)[0];
            if (so != undefined) so.Show = checked;
        }
        this.setState({ data, serviceOptions });
        this.props.onChange(e);
    }

    renderField(obj: IObject, i): any
    {
        const isView = this.props.type === Type.View;
        var name = obj.InputType === "Radio" ? obj.Multilist : obj.Name;
        const objName = obj.Name.split(".");
      
        return <InputField onChange={this.onChange} index={i} inputType={obj.InputType} id={obj.Name} isView={isView}
            label={obj.Label} name={name}  options={obj.Options} data={obj.AlternateName !== undefined ? this.state.data[objName[0] + "." + obj.AlternateName]: this.state.data[obj.Name]}
            helpText={obj.HelpText} validation={obj.Validation} readOnly={obj.Readonly} />;
    }

    renderMultiField(fields, data, i, label): any
    {
        const isView = this.props.type === Type.View;
        var helpText = "";
        fields.filter(x => x.HelpText.length > 0).forEach((x: any, i: number, arr: any[]) =>
        {
            helpText += x.HelpText + (i < arr.length - 1 ? "\n" : "");
        });
        return <div key={"label_" + i + label} className="col"><MultilistSection onChange={this.props.onChange} data={data} isView={isView} fields={fields} label={label} helpText={helpText} />
        </div>;
    }

    renderServiceOptions()
    {
        return this.state.serviceOptions != undefined && this.state.serviceOptions.length > 0 ? <Tabs type={this.props.type} onChange={this.props.onChange} data={this.state.data} columns={this.state.columns} options={this.state.serviceOptions} /> : null;
    }

    renderRows()
    {
        const fields: any[] = [];
        const noMultilistItems = _.orderBy(this.state.sectionMetadata.filter((x: any) =>
        {
            return (x.Multilist !== undefined && x.Multilist.length === 0) ||
                x.InputType === 'Radio' ||
                x.InputType === 'Tag'
        }),
            "SortOrder");

        for (let i = 0; i < noMultilistItems.length; i++)
        {
            fields.push(<div key={i} className="col">{this.renderField(noMultilistItems[i], i)}</div>);
        }
        this.buildMultiList(fields, this.state.sectionMetadata, this.props.data);
        return (<div className={"borderBottom flex-row columns_" + this.props.columns}>
            {fields}
        </div>
        );
    }

    buildMultiList(fields: any[], metaData: any, data: any)
    {
        const multiFields: any[] = metaData.filter((meta: any) => { return meta.Multilist !== undefined && meta.InputType !== 'Radio' && meta.InputType !== 'Tag' && meta.Multilist.length > 0 });
        var groups: any[] = [];
        multiFields.forEach((field: any) =>
        {
            if (groups.findIndex((findIndex: any) => findIndex === field.Multilist) === -1)
                groups.push(field.Multilist);
        });
        groups.forEach((group: any) =>
        {
            var getFields: any[] = multiFields.filter((meta: any) => { return meta.Multilist === group });
            var lowestIndex = getFields.sort((a: any, b: any) => { return a.SortOrder - b.SortOrder })[0].SortOrder;
            fields.splice(lowestIndex - 1, 0, this.renderMultiField(getFields, data, lowestIndex, group));
        });

    }

    render()
    {
        if (!this.state.data || !this.state.sectionMetadata)
        {
            return null;
        }

        const hiddenItems = this.props.hiddenItems != null ? this.props.hiddenItems : this.state.sectionMetadata.length;
        return (<div className="section">
            {this.props.showTitle ? <div className="toggle-heading" onClick={this.toggleSection}>
                <div>
                    {this.state.open ? <i className={fa.faChevronDown}></i> : <i className={fa.faChevronRight}></i>} {" "}
                    {this.props.name}
                </div>
                {!this.state.open ? <div className="toggle-hidden-text">{hiddenItems} {hiddenItems === 1 ? "Field" : "Fields"} Hidden</div> : null}
            </div> : null}
            {this.state.open ?
                <div className="section-body">
                    {this.renderRows()}
                    {this.renderServiceOptions()}
                </div> : null
            }
        </div>);
    }
}


export default Section;


