import * as React from 'react';
import HelpText from './HelpText';
import { ValidationEnum } from "./InputField"

const Checkbox = ({ label, name, checked, onChange, disabled, helpText, required, readOnly }: { label: any, name: any, checked: any, onChange: any, disabled: boolean, helpText?: string | undefined, required: ValidationEnum, readOnly?: boolean }) => {
    name = name == undefined ? "" : name;
    label = label == undefined ? "" : label;
    checked = checked == undefined ? false : checked;
    readOnly = readOnly === undefined ? false : readOnly;
    let cssClass = "";
    if (required === ValidationEnum.Correlated) {
        cssClass = "correlated";
    } else if (required === ValidationEnum.ImmediateRequired) {
        cssClass = "required-now";
    } else if (required === ValidationEnum.FutureRequired) {
        cssClass = "required-future";
    }
    const readOnlyClass = readOnly ? "readonly" : "";

    return (
        <span className={"inner-row " + cssClass}>
            <div className={"label " + readOnlyClass}>
                <label htmlFor={name}>
                    {label}
                </label>
            </div>
            <HelpText helpText={helpText} />
            <div className="value">
                <input type="checkbox" readOnly={readOnly} onChange={onChange} id={name} name={name} checked={checked} disabled={disabled} />
            </div>
        </span>
    );
}


export default Checkbox;


