import * as React from 'react';

export interface IErrorsProps {
    errors?: string[]
}

interface IErrorsState {
    animationSet: boolean;
}

class Errors extends React.Component<IErrorsProps, IErrorsState> {

    constructor() {
        super();
        this.state = {
            animationSet: false
        }
    }

    componentWillReceiveProps(nextProps: IErrorsProps) {
        if (this.myErrors === null || this.myErrors === undefined)
            return;

        if (nextProps.errors !== this.props.errors)
            this.myErrors.classList.remove("animate");

        setTimeout(() => {
            if (this.myErrors != null)
                this.myErrors.classList.add("animate");
        },
            100);
    }

    render() {
        if (this.props.errors === undefined || this.props.errors.length === 0)
            return null;

        return <div className="box errors animate" ref={x => this.myErrors = x}>
            <div className="header">Error Summary</div>
            <div className="body">
                {this.props.errors.map((err: string, i: number) => {
                    return (<div key={i + "_errors"} className="error">{err}</div>);
                })}
            </div>
        </div>;
    }

    myErrors: HTMLDivElement | null = null;


}

export default Errors;


