import * as React from 'react';
import { IStageLog } from '../api/IApi';
import * as moment from "moment";

interface IStageLogsProps
{
    stageLogs?: IStageLog[];
    isLoaded: boolean;
}

class StageLogs extends React.Component<IStageLogsProps, any> {

    constructor(props: IStageLogsProps)
    {
        super(props);
    }
    
    render()
    {
        if (this.props.stageLogs === undefined || this.props.stageLogs === null)
            return null;

        const filteredLogs = this.props.stageLogs.sort((a: IStageLog, b: IStageLog) => { return b.createdDate - a.createdDate });
        if (filteredLogs === undefined)
            return null;
        return (<div className="box">
            <div className="header">
                Stage Logs
            </div>
            <div className="body">
                <table className="table mb20">
                    <thead>
                        <tr className="headerRow">
                            <th>Created Date</th>
                            <th>Created By</th>
                            <th>Stage</th>
                            <th>Cycle Time</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredLogs.map((log: IStageLog, i: number) =>
                        {
                            return <tr key={i} className="dataRow">
                                <td>{moment(log.createdDate).format("MM-DD-YYYY hh:mm A")}</td>
                                <td><a href={`/${log.createdBy.id}`} target="_blank">{log.createdBy.Name}</a></td>
                                <td>{log.stage.name}</td>
                                <td>{log.timeInBusinessDaysExpanded}</td>
                            </tr>
                        })}
                        {this.props.stageLogs === undefined || this.props.stageLogs.length === 0 ?
                            <tr className="dataRow">
                                <td colSpan={4}>No logs available for this product</td>
                            </tr> : null}
                    </tbody>
                </table>
            </div>
        </div>);
    }
}

export default StageLogs;


