import * as React from "react";
import * as fa from './FontAwesome';

export interface IButton {
    onClick();
    className?: string;
    disabled?: boolean;
    loading?: boolean;
    title?: string;
}

class Button extends React.Component<IButton, any> {
    render() {
        return (
            <button className={this.props.className} onClick={this.props.onClick} disabled={this.props.disabled || this.props.loading} title={this.props.title} type="button">
                {this.props.loading ? <i className={fa.combine(fa.faSpin, fa.faCircleONotch)}></i> : null}
                {"  "}
                {this.props.children}
            </button>
        );
    }
}

export default Button;


