import logger, { LogState } from "./Logger";

export interface IDataBuilder
{
    buildPageData(data: any, type: string): IDataReturn[];
    buildSecondaryData(data: any, isNew: boolean): IDataReturn[];
    buildNextStageData(nextStageId: string, recordJsonId: string): IDataReturn;
}

export interface IDataReturn
{
    FieldName: string;
    FieldValue: string | number | boolean;
    ObjectId: string;
    ParentId: string;
    RelatedId: string;
    ValidateNow: boolean;
    Correlated: boolean;
}

interface IValidations
{
    Correlated: boolean;
    Immediate: boolean;
    ParentId: string;
}

class DataBuilder implements IDataBuilder
{
    buildNextStageData(nextStageId: string, recordJsonId: string): IDataReturn
    {
        const fieldObj: IDataReturn = Object.create({});
        fieldObj.FieldName = "TM_Service_Request__c.Stage__c";
        fieldObj.FieldValue = nextStageId;
        fieldObj.ObjectId = recordJsonId;
        fieldObj.ParentId = "";
        fieldObj.RelatedId = "";

        return fieldObj;
    }

    buildSecondaryData(data, isNew: boolean): IDataReturn[]
    {
        const detailsObject: IDataReturn[] = [];
        logger.log(LogState.Debug, "build secondary data", data, isNew);
        data.parentDetails.EditMetadata.Sections.forEach(sec =>
        {
            data.parentDetails.Data.forEach(data =>
            {
                sec.Fields.filter(x => x.InputType !== "Blank" && !x.Readonly).map(field =>
                {
                    const fieldObj: IDataReturn = Object.create({});
                    fieldObj.FieldName = field.Name;
                    fieldObj.FieldValue = this.getValue(data[field.Name], field.InputType);
                    fieldObj.ObjectId = data[field.ObjectId] == undefined ? "" : data[field.ObjectId];
                    fieldObj.ParentId = data["ParentId"] == undefined ? "" : data["ParentId"];
                    fieldObj.RelatedId = "";
                    if (field["Validation"] !== undefined)
                    {
                        fieldObj.ValidateNow = field.Validation.Immediate;
                        fieldObj.Correlated = field.Validation.Correlated;
                    }
                    detailsObject.push(fieldObj);
                });
            });
        });

        data.childDetails.EditMetadata.Sections.forEach(sec =>
        {
            data.childDetails.Data.forEach(data =>
            {
                sec.Fields.filter(x => x.InputType !== "Blank" && !x.Readonly).map(field =>
                {
                    const fieldObj: IDataReturn = Object.create({});
                    fieldObj.FieldName = field.Name;
                    fieldObj.FieldValue = this.getValue(data[field.Name], field.InputType);
                    fieldObj.ObjectId = isNew || data[field.ObjectId] == undefined ? "" : data[field.ObjectId];
                    fieldObj.ParentId = data["ParentId"] == undefined ? "" : data["ParentId"];
                    fieldObj.RelatedId = data["RelatedId"] == undefined ? "" : data["RelatedId"];
                    if (field["Validation"] !== undefined)
                    {
                        fieldObj.ValidateNow = field.Validation.Immediate;
                        fieldObj.Correlated = field.Validation.Correlated;
                    }
                    detailsObject.push(fieldObj);
                });
            });
        });
        return detailsObject;

    }

    buildPageData(data, type: string): IDataReturn[]
    {
        const mainPageObjects: IDataReturn[] = [];
        const objects = ["details", "accounts", "contacts"];
        objects.forEach(objs =>
        {
            if (data[objs + type])
            {
                if (data[objs] != undefined)
                {
                    data[objs].EditMetadata.Sections.forEach(sec =>
                    {
                        if (sec.DependentFields != undefined)
                        {
                            sec.DependentFields.forEach(df =>
                            {
                                df.Fields.filter(x => x.InputType !== "Blank" && !x.Readonly).map(f =>
                                {
                                    const fieldObj: IDataReturn = Object.create({});
                                    fieldObj.FieldName = f.Name;
                                    
                                    fieldObj.FieldValue = this.getValue(data[objs].Data[0][f.Name], f.InputType);
                                    fieldObj.ObjectId = data[objs].Data[0][f.ObjectId] == undefined ? "" : data[objs].Data[0][f.ObjectId];
                                    fieldObj.ParentId = "";
                                    fieldObj.RelatedId = "";
                                    if (f["Validation"] !== undefined)
                                    {
                                        fieldObj.ValidateNow = f.Validation.Immediate;
                                        fieldObj.Correlated = f.Validation.Correlated;// || f.Validation.ParentId !== "";
                                    }
                                    mainPageObjects.push(fieldObj);
                                });
                            });
                        }

                        data[objs].Data.forEach(d =>
                        {
                            sec.Fields.filter(x => x.InputType !== "Blank" && !x.Readonly).map(field =>
                            {
                                const fieldObj: IDataReturn = Object.create({});
                                fieldObj.FieldName = field.Name;
                                fieldObj.FieldValue = this.getValue(d[field.Name], field.InputType);
                                fieldObj.ObjectId = d[field.ObjectId] == undefined ? "" : d[field.ObjectId];
                                fieldObj.ParentId = "";
                                fieldObj.RelatedId = "";
                                if (field["Validation"] !== undefined)
                                {
                                    fieldObj.ValidateNow = field.Validation.Immediate;
                                    fieldObj.Correlated = field.Validation.Correlated;// || field.Validation.ParentId !== "";
                                }
                                mainPageObjects.push(fieldObj);
                            });
                        });


                    });
                }
            } else
            {
                if (data[objs] != undefined)
                {
                    data[objs].ViewMetadata.Sections.forEach(() =>
                    {
                        data[objs].Data.filter(x => Object.getOwnPropertyNames(x).length !== 0).forEach(d =>
                        {
                            const fieldObj: IDataReturn = Object.create({});
                            fieldObj.FieldName = "Selected";
                            fieldObj.FieldValue = d.Included.Selected == undefined ? false : d.Included.Selected;
                            fieldObj.ObjectId = d.Included.Id == undefined ? "" : d.Included.Id;
                            fieldObj.ParentId = "";
                            fieldObj.RelatedId = "";
                            mainPageObjects.push(fieldObj);
                        });
                    });
                }
            }
        });
        return mainPageObjects;
    }

    getValue(inputValue: string, inputType: string)
    {
        if (inputValue === undefined && inputType === "Numeric")
            return 0;
        else if (inputValue === undefined)
            return "";
        else if (inputType === "Numeric")
            return Number(inputValue);
        else
            return inputValue;
    }
}

export default DataBuilder;


