import * as React from 'react';
import * as fa from "./FontAwesome";

export interface IButtonGroup {
    showHide(): void;
}

export interface IButtonGroupProps {
    DefaultName: string;
}

export interface IButtonGroupState {
    isOpen: boolean;
}

class ButtonGroup extends React.Component<IButtonGroupProps, IButtonGroupState> implements IButtonGroup {
    buttonGroup: any;

    constructor() {
        super();
        this.state = {
            isOpen: false
        }
        this.showHide = this.showHide.bind(this);
    }

    componentDidMount() {
        document.addEventListener("click",
            (e: any) => {
                let hide = true;
                e.path.forEach((x: any) => {
                    if (x === this.buttonGroup)
                        hide = false;
                });
                if (hide) {
                    this.setState({ isOpen: false });
                }
            });
    }

    showHide() {
        this.setState({ isOpen: !this.state.isOpen });
    }

    render() {
        return (<div className="button-group" ref={(x: any) => this.buttonGroup = x}>
            <div className={"holder" + (this.state.isOpen ? " open" : "")} onClick={this.showHide}>
                <span className="btn">{this.props.DefaultName}</span><span className="btn"><i className={this.state.isOpen ? fa.faChevronUp : fa.faChevronDown}></i></span>
            </div>
            {this.state.isOpen ?
                <div className="buttons">
                    {this.props.children}
                </div> : null}
        </div>);
    }
}

export default ButtonGroup;


