export interface IPdfGenerator
{
    GeneratePacketForm(data: any, entityName: string): boolean;
    GenerateSingleForm(data: any, entityName: string, packetType: string): boolean;
    GenerateContactSummaryForm(data: any, entityName: string): boolean;
    GenerateUserSetupView(data: any, entityName: string): boolean;
}
class PdfGenerator implements IPdfGenerator
{
    GenerateContactSummaryForm(data, entityName: string): boolean
    {
        const fileName = `${entityName}_Packet.pdf`;
        return this.GeneratePdf(data, fileName);
    }

    GeneratePacketForm(data: any, entityName: string): boolean
    {
        const fileName = `${entityName}_Packet.pdf`;
        return this.GeneratePdf(data, fileName);
    }

    GenerateSingleForm(data: any, entityName: string, packetType: string): boolean
    {
        const fileName = `${entityName}_${packetType}.pdf`;
        return this.GeneratePdf(data, fileName);
    }

    GenerateUserSetupView(data: any, entityName: string): boolean
    {
        const fileName = `${entityName}.pdf`;
        return this.GeneratePdf(data, fileName);
    }

    private GeneratePdf(data: string, fileName: string)
    {
        var byteCharacters = atob(data);
        var byteNumbers = new Array(byteCharacters.length);
        for (let i = 0; i < byteCharacters.length; i++)
        {
            byteNumbers[i] = byteCharacters.charCodeAt(i);
        }
        var byteArray = new Uint8Array(byteNumbers);
        var blob = new Blob([byteArray], { type: 'application/pdf' });

        var objectUrl = URL.createObjectURL(blob);

        if (window.navigator.msSaveBlob)
        {
            window.navigator.msSaveOrOpenBlob(blob, fileName);
        } else
        {
            window.open(objectUrl, "_blank");
        }
        setTimeout(() =>
        {
            URL.revokeObjectURL(objectUrl);
        }, 100);

        return true;
    }
}

export default PdfGenerator;


