import { Component } from "react";
import domParser from "./DomParserHelper";

export function isNullOrEmpty(s: string) {
    if (s == undefined || s.length === 0)
        return true;

    return false;
}


export function isNullOrUndefined(o: object) {
    return o == null || o == undefined;
}


export function setStateAsync<T>(o: Component, newState: Partial<T>): Promise<boolean> {
    return new Promise((resolve: any) => {
        o.setState(newState, () => {
            resolve(true);
        })
    });
}

export function JsonParseHelper(k: string, v: string) {
    if (typeof v === "string")
        return domParser.parse(v);

    return v;
}


