import * as React from 'react';
import { Type } from "../Grid";
import InputField from "../common/InputField";
import domParserHelper from '../../lib/DomParserHelper';
import logger, { LogState } from '../../lib/Logger';

interface IServiceOptionTabs {
    options: any;
    columns: number;
    data: any;
    onChange: any;
    type: Type;
}

class ServiceOptionTabs extends React.Component<IServiceOptionTabs, any> {

    constructor() {
        super();
        this.state = {
            selected: undefined,
            data: undefined,
            options: undefined
        }
        this.receiveProps = this.receiveProps.bind(this);
        this.renderCurrent = this.renderCurrent.bind(this);
        this.switchTabs = this.switchTabs.bind(this);
        this.onChange = this.onChange.bind(this);
        this.renderField = this.renderField.bind(this);
        this.renderRows = this.renderRows.bind(this);
    }
    componentWillMount(): void {
        this.receiveProps(this.props);
    }

    componentWillReceiveProps(nextProps): void {
        this.receiveProps(nextProps);
    }

    receiveProps(props) {
        let selected = this.state.selected;
        const tabs = this.props.options.filter(x => this.props.data[x.KeyFieldName] || x.Type === "Cascade").map(x => {
            return x.KeyFieldName;
        });

        if (!(selected != undefined && tabs.filter(x => { return x === selected })[0] != undefined))
            selected = tabs[0];

        this.setState({ selected, data: this.props.data, options: this.props.options });
    }

    renderCurrent() {
        const current = this.state.options.filter(x => x.KeyFieldName === this.state.selected && (x.Type === "Cascade" || this.state.data[x.KeyFieldName]))[0];
        if (current === undefined)
            return null;
        return <div>
            {this.renderRows(current.Type, current.KeyFieldName, current.Fields)}
        </div>;
    }

    switchTabs(e) {
        this.setState({ selected: e.target.id });
    }

    renderRows(type: string, keyFieldName: string, metaData) {
        const fields: any[] = [];
        for (let i = 0; i < metaData.length; i++) {
            let readOnly = type === "Cascade" && !metaData[i].Correlated || type === "Cascade" && metaData[i].Correlated && !this.state.data[keyFieldName];
            fields.push(<div key={i} className="col">{this.renderField(metaData[i], i, readOnly)}</div>);
        }
        return (<div className={"bottomBorder flex-row columns_" + this.props.columns}>
            {fields}
        </div>
        );
    }

    onChange(e) {
        const data = this.state.data;
        const name = e.target.name;
        const value = e.target.value;
        const checked = e.target.checked;
        data[name] = e.target.type === "checkbox" ? checked : value;
        this.setState({ data });
        this.props.onChange();
    }

    renderField(obj, i, readOnly: boolean): any {
        const isView = this.props.type === Type.View;
        return <InputField readOnly={readOnly || obj.Readonly} validation={obj.Validation} onChange={this.onChange} id="" index={i} inputType={obj.InputType} isView={isView} label={obj.Label} name={obj.Name} options={obj.Options} data={this.state.data[obj.Name]} />;
    }

    render() {
        if (this.state.data == undefined)
            return null;
        return (
            <div className="tab-holder">
                <ul className="tabs">
                    {this.state.options.filter(x => this.state.data[x.KeyFieldName] || x.Type === "Cascade").map((x, i) => {
                        return <li key={i} className={this.state.selected === x.KeyFieldName ? "active" : ""}><a id={x.KeyFieldName} onClick={this.switchTabs}>{x.Label || x.Name}</a></li>;
                    })}
                </ul>
                <div className="tab-content">
                    {this.renderCurrent()}
                </div>
            </div>);
    }
}

export default ServiceOptionTabs;


