import * as React from 'react';
import Checkbox from './common/CheckBox';
import { ValidationEnum } from './common/InputField';
import Dropdown, { IOption } from './common/Dropdown';
import Textbox from './common/Textbox';
import { isNullOrEmpty, isNullOrUndefined, setStateAsync } from "../lib/Helpers";
import { ITmoSigner } from '../api/IApi';
import TMRespository, { ITMRepository } from "../api/TMRepository";

interface IOutForSignatureProps {
    packetId: string;
    contacts: ContactsData[];
    requests: PacketRequests[];
    nextStageId: string;
    onSignerChange: (signerInfo: ISignerInfo) => void;
    lastPrintDate?: number;
    tmRepository: ITMRepository;
}

enum SignerType {
    Primary,
    Secondary,
    Tmo
}

export interface PacketRequests {
    Id: string;
    Name: string;
    Stage: string;
    PrimarySignerCode: string;
    SecondarySignerCode: string;
    TmoSignerCode: string;
    StageDetails: IStageDetails;
}

export interface IStageDetails {
    Id: string;
    Name: string;
    StageType: string;
    RelatedStages: IStageDetails[];
}

export interface ContactsData {
    "Contact.Id": string;
    "Contact.FirstName": string;
    "Contact.LastName": string;
    "Contact.Email": string;
    "Contact.MobilePhone": string;
    "Contact.Phone": string;
}

export interface ISignerInfo {
    signer: ISigner[];
    packetId: string;
    tmRequests: string[];
}

export interface ISigner {
    id: string;
    securityCode: string;
    signerCode: string;
    priority: number;
    tmo?: ITmoSigner
}

interface IOutForSignatureState {
    primarySignerSecurityCode: string;
    primarySignerSignerCode: string;
    primarySignerFirstName: string;
    primarySignerLastName: string;
    primarySignerEmail: string;
    primarySignerInit: string;
    primarySignerId: string;
    secondarySignerSecurityCode: string;
    secondarySignerSignerCode: string;
    secondarySignerFirstName: string;
    secondarySignerLastName: string;
    secondarySignerEmail: string;
    secondarySignerInit: string;
    secondarySignerId: string;
    tmoSignerSecurityCode: string;
    tmoSignerSignerCode: string;
    tmoSignerFirstName: string;
    tmoSignerLastName: string;
    tmoSignerEmail: string;
    tmoSignerPhone: string;
    tmoSignerId: string;
    hasSigners: boolean;
    hasTmoSigner: boolean;
    inputs: ICheckBoxState;
    tmoSigners: ITmoSigner[]
}

interface ICheckBoxState {
    [key: string]: { value: boolean };
}

class OutForSignature extends React.Component<IOutForSignatureProps, IOutForSignatureState> {

    constructor(props: IOutForSignatureProps) {
        super(props);
        this.state = {
            inputs: {},
            primarySignerEmail: "",
            primarySignerFirstName: "",
            primarySignerInit: "",
            primarySignerLastName: "",
            primarySignerId: "",
            primarySignerSecurityCode: "",
            primarySignerSignerCode: "",
            secondarySignerEmail: "",
            secondarySignerFirstName: "",
            secondarySignerInit: "",
            secondarySignerLastName: "",
            secondarySignerId: "",
            secondarySignerSecurityCode: "",
            secondarySignerSignerCode: "",
            tmoSignerEmail: "",
            tmoSignerPhone: "",
            tmoSignerFirstName: "",
            tmoSignerLastName: "",
            tmoSignerSecurityCode: "",
            tmoSignerSignerCode: "",
            tmoSignerId: "",
            hasSigners: false,
            hasTmoSigner: false,
            tmoSigners: []
        }
    }

    async componentWillMount() {
        const tmos = await this.props.tmRepository.getTmos();

        this.setState({ tmoSigners: tmos });
    }

    getFilteredRequests = (): PacketRequests[] => {
        return this.props.requests.filter((x: PacketRequests) => x.StageDetails.RelatedStages.find(y => y.Id === this.props.nextStageId) != null && x.Stage.toLowerCase() !== 'complete' && x.Stage.toLowerCase() !== "out for signature");
    }

    onChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        let { inputs, hasSigners, hasTmoSigner } = this.state;
        inputs[e.target.name] = { value: e.target.checked };

        hasSigners = this.getCheckedRequestIds().length > 0;
        if (!hasSigners) {
            this.removeSelectedSigner(SignerType.Primary);
            this.removeSelectedSigner(SignerType.Secondary);
        }

        hasTmoSigner = this.getCheckedRequestIds(true).length > 0;
        if (!hasTmoSigner)
            this.removeSelectedSigner(SignerType.Tmo);

        await setStateAsync(this, { inputs, hasSigners, hasTmoSigner });
        this.onSave();
    }

    removeSelectedSigner = async (type: SignerType) => {
        let stateObj = {} as IOutForSignatureState;
        stateObj[`${SignerType[type].toLowerCase()}SignerId`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerFirstName`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerLastName`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerEmail`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerSecurityCode`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerSignerCode`] = "";
        stateObj[`${SignerType[type].toLowerCase()}SignerPhone`] = "";
        await setStateAsync<IOutForSignatureState>(this, stateObj);
    }

    onChangeTmoSigner = async (e: React.ChangeEvent<HTMLSelectElement>) => {
        const signerTmo = this.getTmoSigner(e.target.value);

        if (signerTmo == undefined) {
            this.removeSelectedSigner(SignerType.Tmo);
            return;
        }

        const fakeContact = {
            "Contact.Phone": signerTmo.Phone, "Contact.Id": "",
            "Contact.FirstName": "", "Contact.LastName": "", "Contact.Email": "",
            "Contact.MobilePhone": undefined
        };

        let stateObj = {} as IOutForSignatureState;
        stateObj[`tmoSignerFirstName`] = signerTmo.FirstName;
        stateObj[`tmoSignerLastName`] = signerTmo.LastName;
        stateObj[`tmoSignerEmail`] = signerTmo.Email;
        stateObj[`tmoSignerSecurityCode`] = this.getSecurityCode(fakeContact);
        stateObj[`tmoSignerPhone`] = signerTmo.Phone;
        stateObj[`tmoSignerSignerCode`] = this.getSignerCode(SignerType.Tmo);

        await setStateAsync(this, stateObj);
        this.onSave();
    }

    onChangeSigner = async (e: React.ChangeEvent<HTMLSelectElement>) => {
        if (isNullOrUndefined(this.props.contacts))
            return;

        const name = e.currentTarget.name;
        const value = e.currentTarget.value;

        const type = name === "primary" ? SignerType.Primary : SignerType.Secondary;

        if (value.length === 0) {
            await this.removeSelectedSigner(type);
            this.onSave();
        }

        const contact = this.props.contacts.find(x => x["Contact.Id"] === value);

        if (contact === undefined)
            return;

        const signerTypeName = SignerType[type].toLowerCase() || "";

        let stateObj = {} as IOutForSignatureState;
        stateObj[`${signerTypeName}SignerId`] = contact["Contact.Id"];
        stateObj[`${signerTypeName}SignerFirstName`] = contact["Contact.FirstName"];
        stateObj[`${signerTypeName}SignerLastName`] = contact["Contact.LastName"];
        stateObj[`${signerTypeName}SignerEmail`] = contact["Contact.Email"];
        stateObj[`${signerTypeName}SignerSecurityCode`] = this.getSecurityCode(contact);
        stateObj[`${signerTypeName}SignerSignerCode`] = this.getSignerCode(type);

        await setStateAsync(this, stateObj);
        this.onSave();
    }

    getSignerCode = (type: SignerType): string => {
        if (type === SignerType.Primary) {
            const found = this.props.requests.find((x: PacketRequests) => !isNullOrEmpty(x.PrimarySignerCode));
            if (found !== undefined)
                return found.PrimarySignerCode;
        }

        if (type === SignerType.Secondary) {
            const found = this.props.requests.find(x => !isNullOrEmpty(x.SecondarySignerCode));
            if (found !== undefined)
                return found.SecondarySignerCode;
        }

        if (type === SignerType.Tmo) {
            const found = this.props.requests.find(x => !isNullOrEmpty(x.TmoSignerCode));
            if (found !== undefined)
                return found.TmoSignerCode;
        }

        return "";
    }

    getSecurityCode = (contact: ContactsData): string => {

        if (!contact)
            return "0000";

        var phone = undefined;

        if (contact["Contact.Phone"])
            phone = contact["Contact.Phone"];

        if (contact["Contact.MobilePhone"] != undefined)
            phone = contact["Contact.MobilePhone"];

        if (phone != undefined) {
            const ph = phone.replace(/[^\d]/g, "") as string;
            if (ph === undefined || ph === null)
                return "0000";
            var firstMatch = ph.match(/[\d]{4}$/g);
            if (firstMatch === null)
                return "0000";

            return firstMatch[0];
        };

        return "0000";
    }

    onChangeSecurityCode = async (e: React.ChangeEvent<HTMLInputElement>) => {
        var value = e.target.value.replace(/[^\d]/g, "");
        var name = e.target.name;
        var state = this.state;
        state[name] = value;
        if (value.length < 7) {
            await setStateAsync(this, state);
            this.onSave();
        }
    }


    getSigners = (allowBlank: boolean = false): IOption[] => {
        const options = [{ Label: "--None--", Value: "", Enabled: allowBlank }] as IOption[];

        const mapValues = this.props.contacts.map((x: ContactsData) => {
            return { Label: `${x["Contact.FirstName"]} ${x["Contact.LastName"]} (${x["Contact.Email"]})`, Value: x["Contact.Id"] } as IOption;
        });
        options.push(...mapValues);
        return options;
    }

    getTmoSigner = (email: string): ITmoSigner => {
        return this.state.tmoSigners.find(x => x.Email === email);
    }

    getTmoSigners = (): IOption[] => {
        const options = [{ Label: "--None--", Value: "", Enabled: true }] as IOption[];
        const tmos = this.state.tmoSigners;
        tmos.forEach(x => {
            options.push({ Label: `${x.FirstName} ${x.LastName} (${x.Email})`, Value: x.Email, Enabled: true } as IOption);
        });

        return options;
    }

    getCheckedRequestIds = (tmsa: boolean = false): string[] => {
        const { inputs } = this.state;
        const tmRequests = Object.getOwnPropertyNames(inputs);
        const tmRequestIds: string[] = [];
        for (const t of tmRequests) {
            if (tmsa && t === "tmsa" && inputs[t].value)
                tmRequestIds.push(t)
            else if (inputs[t].value && t !== "tmsa" && !tmsa)
                tmRequestIds.push(t);
        }

        return tmRequestIds;
    }

    onSave = () => {
        const tmRequestIds = this.getCheckedRequestIds();
        const { primarySignerId, secondarySignerId, tmoSignerEmail, hasTmoSigner } = this.state;
        const signerInfo = {} as ISignerInfo;

        signerInfo.packetId = this.props.packetId;
        signerInfo.tmRequests = tmRequestIds;
        signerInfo.signer = [];

        if (primarySignerId.length > 0) {
            signerInfo.signer.push({
                id: this.state.primarySignerId,
                securityCode: this.state.primarySignerSecurityCode,
                priority: 1,
                signerCode: this.state.primarySignerSignerCode
            });
        }

        if (secondarySignerId.length > 0) {
            signerInfo.signer.push({
                id: this.state.secondarySignerId,
                securityCode: this.state.secondarySignerSecurityCode,
                priority: 2,
                signerCode: this.state.secondarySignerSignerCode
            });
        }

        if (tmoSignerEmail.length > 0 || hasTmoSigner) {
            signerInfo.signer.push({
                id: "",
                securityCode: this.state.tmoSignerSecurityCode,
                priority: 3,
                signerCode: this.state.tmoSignerSignerCode,
                tmo: {
                    FirstName: this.state.tmoSignerFirstName,
                    LastName: this.state.tmoSignerLastName,
                    Email: this.state.tmoSignerEmail,
                    Phone: this.state.tmoSignerPhone
                }
            });
        }

        this.props.onSignerChange(signerInfo);
    }

    render() {
        const tmsaChecked = this.state.inputs["tmsa"] == undefined ? false : this.state.inputs["tmsa"].value;
        const tmsaLastPrint = this.props.lastPrintDate !== undefined ? new Date(this.props.lastPrintDate).toLocaleDateString() : "Never";
        return <div><hr /><h2>Choose products for digital signature</h2>
            <div>
                <div>
                    <Checkbox label={`TMSA (Last Signed: ${tmsaLastPrint})`} name="tmsa" checked={tmsaChecked} onChange={this.onChange} helpText="" disabled={false} required={ValidationEnum.NotRequired} />
                </div>
            </div>
            {this.getFilteredRequests().map((x: PacketRequests, i: number) => {
                var checked = this.state.inputs[`${x.Id}`] == undefined ? false : this.state.inputs[`${x.Id}`].value;
                var disabled = x.Stage.toLocaleLowerCase() === 'out for signature';

                return (<div key={i}>
                    <div><Checkbox label={`${x.Name} (${x.Stage})`} name={x.Id} checked={checked} onChange={this.onChange} disabled={disabled} helpText="" required={ValidationEnum.NotRequired} />   </div>
                </div>)
            })}

            {this.state.hasSigners ? <div>
                <hr />
                <div>
                    <Dropdown options={this.getSigners()} name="primary" disabled={false} label="Primary Signer"
                        selected={this.state.primarySignerId} helpText="" required={ValidationEnum.NotRequired} onChange={this.onChangeSigner} />
                </div>
                <Textbox helpText="(Default security code is last four of mobile number or phone number, whichever is available respectively.) Enter Security code 0-9 with length 4-6"
                    isNumeric={true} label="Primary Signer Security Code" name="primarySignerSecurityCode" text={this.state.primarySignerSecurityCode}
                    onChange={this.onChangeSecurityCode} required={ValidationEnum.NotRequired}  />
                <hr />
                <div>
                    <Dropdown options={this.getSigners(true)} name="secondary" disabled={false} label="Secondary Signer (optional)"
                        selected={this.state.secondarySignerId} helpText="" required={ValidationEnum.NotRequired} onChange={this.onChangeSigner} />
                </div>
                <Textbox helpText="(Default security code is last four of mobile number or phone number, whichever is available respectively.) Enter Security code 0-9 with length 4-6"
                    isNumeric={true} label="Secondary Signer Security Code" name="secondarySignerSecurityCode"
                    text={this.state.secondarySignerSecurityCode} onChange={this.onChangeSecurityCode} required={ValidationEnum.NotRequired}
                     />
            </div> : null}

            {this.state.hasTmoSigner ? <div>
                <hr />
                <div>
                    <Dropdown options={this.getTmoSigners()} name="tmo" disabled={false} label="Bank Signer"
                        selected={this.state.tmoSignerEmail} helpText="" required={ValidationEnum.NotRequired} onChange={this.onChangeTmoSigner} />
                </div>
                <Textbox helpText="(Default security code is last four of mobile number or phone number, whichever is available respectively.) Enter Security code 0-9 with length 4-6"
                    isNumeric={true} label="Bank Signer Security Code" name="tmoSignerSecurityCode"
                    text={this.state.tmoSignerSecurityCode} onChange={this.onChangeSecurityCode} required={ValidationEnum.NotRequired}
                     />
            </div> : null}
        </div>
    }

}

export default OutForSignature;




